module.exports = [{"category":"skattamal","name":"Davki","statement":"<p>Flokkur flksins krefst ess a htt veri a skattleggja ftkt. Undanfarna ratugi hafa skattleysismrk ekki teki breytingum  rttu samrmi vi run verlags og kaupmttar. Vegna ess er veri a skattleggja ftkt. Afnumdar veri skeringar  atvinnutekjum eldri borgara, ryrkja og nmsmanna.</p>\n<p>Vi viljum sna vi eirri fugrun a persnuafslttur rrni r fr ri  vergildi og hkki annig skattbyri mest hj eim sem lgstar hafa tekjurnar. Flokkur flksins vill a skattleysismrk veri hkku  350.000 kr.  mnui.</p>\n<p>Til a fjrmagna  breytingu veri tekinn upp fallandi persnuafslttur. annig vera lgmarkslaun skattfrjls en flk  ofurlaunum fr engan skattaafsltt. </p>\n"},{"category":"jafnrettismal","name":"Enakopravnost spolov","statement":""},{"category":"stjornarskrarmal","name":"Epidemilogija","statement":"<p>Flokkur flksins vill standa vr um sjlfsti slands. Vi munum standa gegn llum tillgum sem skert geta fullveldisrtt slendinga yfir aulindum snum.</p>\n<p>Vi viljum stula a byrgum breytingum  stjrnarskrnni me vtkri samstu.</p>\n<p>Vi leggjum herslu  a aulindakvi veri btt inn  stjrnarskrna. a er skilyrislaus krafa  eim efnum a aulindirnar veri skilgreindar sem jareign er megi aldrei framselja me varanlegum htti og a fyrir afnot af eim veri greitt fullt ver.</p>\n<p>vallt ber a standa af rvekni vr um kvi stjrnarskrrinnar er vara rttindi einstaklinganna. Rka herslu ber a leggja  viringu fyrir sjlfstum rtti einstaklingsins. Smu rttindi skulu gilda fyrir alla, h stjrnmlaskounum, tr, litarhtti, kynferi ea kynhneig.</p>\n"},{"category":"evropumal","name":"Evropska integracija","statement":"<p>Flokkur flksins styur ekki aild slands a ESB. </p>\n<p>Vi erum andvg v a sland innleii orkustefnu ESB og veri  orkusambandi ESB og andvg v a sland innleii ESB-lg um raforkuviskipti yfir landamri og a landi lti Evrpusambandsstofnun fyrir samvinnu orkueftirlitsaila (ACER). Rkin eru au a  slandi eru engin raforkuviskipti yfir landamri ar landi er ekki tengt ESB me sstreng og v ekki hgt a selja raforku til ESB-rkja. </p>\n<p>Mikilvgt er a sland hugi a sjlfsti snu og fullveldi  samskiptum snum vi ESB  gegnum EES-samninginn lkt og  samskiptum snum vi nnur rkjasambndog strveldi. Hagsmunum slendinga er best borgi sem sjlfstri og fullvalda j.</p>\n"},{"category":"sjavarutvegsmal","name":"Gospodarstvo","statement":"<p>Flokkur flksins vill f kvtann aftur heim og a greitt s fullt ver fyrir agang a sjvaraulindinni!</p>\n<p>Vi viljum nja ntingarstefnu fiskimianna ar sem aulindirnar okkar eru sameign jarinnar en ekki einkaeign frra tvalinna sgreifa eins og n httar til.</p>\n<p>Vi munum beita okkur fyrir v a bar sjvarbygga njti aukins rttar til a nta sjvaraulindina me jkvum hrifum  sjvarplssin vs vegar um land.</p>\n<p>Vi tlum a strefla strandveiar og gera handfraveiar frjlsar.\nVi krefjumst ess a jin fi fullt ver fyrir sameiginlegan agang a aulindum hennar.</p>\n<p>Vi styjum lgfestingu kvis um jareign  aulindum  stjrnaskr.</p>\n"},{"category":"samgongumal","name":"Infrastruktura","statement":"<p>Flokkur flksins leggur herslu  a umferarryggi og reianleiki su vallt  forgangi  samngumlum. Vegalengdir milli ttbliskjarna og byggarlaga veri styttar og bttar eins og kostur er.</p>\n<p>Flokkur flksins vill a stugt s unni  a v a bta umferarryggi  jvegum landsins meal annars me bttu vihaldi, breikkun vega, fkkun einbreira bra  jvegi 1 og frekari lagningu bundins slitlags.</p>\n<p>Mikilvgt er a rast sem fyrst  fulla ger Sundabrautar og a bra lfus noran Selfoss. Einnig a vegamlum sunnanverra Vestfjara veri varanlega komi  gott horf.</p>\n<p>Einkum er brnausynlegt a leggja Sundabraut, bi af ryggis- og hagkvmnistum. a er forsvaranlegt af borgaryfirvldum  Reykjavk hve mjg au hafa tafi fyrir  essum efnum. Borgaryfirvld hafa treka komi  veg fyrir framkvmdir og m.a. gripi til ess a thluta lum  fyrirhuguu vegsti Sundabrautar. etta hefur aftur hamla uppbyggingu nrra basva norur af nverandi hfuborgarsvi, s. s.  Geldinganesi, Kjalarnesi og jafnvel noran Hvalfjarar.</p>\n<p>Flokkur flksins styur jargangager  Austurlandi, Vestfjrum og var til a tryggja ruggar samgngur  landsbygginni. Jargng eru ekki munaarvara heldur ryggisatrii.</p>\n<p>Reykjavkurflugvllur  heima  Vatnsmrinni og skal fram gegna lykilhlutverki sem mist samgangna og sjkraflutninga  lofti fyrir alla landsmenn, og einnig sem varaflugvllur fyrir Keflavkurflugvll.</p>\n"},{"category":"utlendingamal","name":"Migracije","statement":"<p>Flokkurinn leggur herslu  a innflytendur eigi greia lei a algun a slensku samflagi. Lykilinn a v er a flki s gert kleift a vera tttakendur  slensku samflagi, t. d.  vinnumarkai, a flk njti tkifra til menntunar og ess s gtt a a eigi kost  a tileinka sr kunnttu  slensku. Innflytjendur skulu hvattir til slenskunms enda er kunntta  mlinu mikilvgur lykill a v a hver og einn geti ori fullur tttakandi  samflaginu. Flokkur flksins leggur herslu  a srstaklega s hl a brnum innflytjenda annig a eim li vel  slensku samflagi.</p>\n<p>Vi leggjum herslu  a haldi s uppi flugri landamragslu  slandi.</p>\n<p>Vi viljum a ger veri ttekt  Schengensamstarfinu me gagnrnum huga og a meti vandlega hvort sland haldi fram ttku.</p>\n<p>Afgreisla hlisumskna skal vera skilvirk. Halda verur  skefjum eim kostnai sem keyrt hefur r hfi vegna tilhfulausra umskna. Afgreia  slkar umsknir innan 48 stunda eins og gert  ngrannalndum. sland  ekki a hafa lgri rskuld en arar jir.</p>\n"},{"category":"sottvarnarmal","name":"Obramba","statement":"<p>Flokkur flksins telur rtt a fari s a rum fagflks, ar me tali sttvarnalknis, varandi agerir gegn COVID-19. kvaranataka og byrg hltur  vallt a hvla  framkvmdavaldinu, rkisstjrn og rherrum hennar.</p>\n<p>Vi viljum sj meira samr vi lggjafann, Alingi, en veri hefur  essum efnum. Hlutverk lggjafans er a koma me tillgur og veita stjrnvldum ahald m.a. me umru. ll umra um essi ml er af hinu ga. Barttan gegn COVID-19 er samflagslegt verkefni. Flokkur flksins hefur fr upphafi stutt strangar agerir  eirri vinnu svo halda megi smiti innanlands  algeru lgmarki og helst trma eim.</p>\n"},{"category":"loftslagsmal","name":"Podnebne spremembe","statement":"<p>Flokkur flksins ltur  loftlagsbreytingar af mannavldum sem grafalvarlegan, hnattrnan umhverfisvanda enda virir mengun engin landamri.</p>\n<p>Vi viljum a slendingar taki virkan tt  barttunni gegn essari v  aljlegu samstarfi. Endurheimt votlendis, rafving blaflotans, vistvn orka til skipa, framleisla  endurnjanlegri orku og aukin skgrkt er r mtagerir sem liggur beinast vi slendingar rist .</p>\n<p>Vi leggjum mikla herslu  a sland axli byrg  barttunni gegn loftslagsbreytingum n ess a agerir  gu umhverfisverndar bitni  almenningi sem hefur r litlu a moa fjrhagslega. a hafa t.d. ekki allir efni  v a skipta yfir  rafbla.</p>\n<p>Vi munum v beita okkur gegn grnum skttum sem auka misskiptingu og ftkt. eir greii mest sem menga mest.</p>\n<p>Vi viljum nta hreinar orkulindir landsins af skynsemi svo draga megi r mengun, samhlia nttruvernd.</p>\n<p>ar sem hlendisjgarur hefur veri tengdur loftslagsumru, er vert a taka fram a vi styjum ekki takmarkanir  ferafrelsi almennings innanlands til a njta eigin nttru. Hlendi er unasreitur sem m ekki stofnanava  formi jgars me tilheyrandi rherrari  kostna almannarttar. Vi treystum heimamnnum  nrliggjandi sveitarflgum vel til a halda utan um hlendismlin.</p>\n<p>a hafa ekki allir efni  utanlandsferum og v er grarlega mikilvgt a almenningur, og  ekki sst fatla flk, hafi greian agang a essari endurnrandi parads hlendisins n gjaldtku. slendingar hafa noti hennar lengi, til dmis innan vbanda tivistarflaga, og almennt gengi vel um. Vi treystum essu flki til a njta hlendisins, vira a fram og vernda  senn.  </p>\n"},{"category":"byggdarmal","name":"Pokrajine","statement":"<p>Flokkur flksins mun alltaf verja heimilin! Vi tlum a afnema vertryggingu hsnislna. Vi viljum a almenningi veri heimilt a endurfjrmagna vertrygg ln me vertryggum lnum n ess a undirgangast lnshfis- og greislumat.</p>\n<p>Vi tlum a afnema me llu himinh uppgreislugjld  lnasamningum sem balnasjur geri  snum tma.</p>\n<p>Vi leggjum herslu  rttmikla atvinnustarfsemi um allt land  landbnai, inai, sjvartvegi, ferajnustu og skapandi greinum.</p>\n<p>Til ess a bygg haldist  landinu llu er brnt a starfsemi hins opinbera ti  landi veri  fullu samrmi vi arfir banna, fjlda eirra og samgngur  vikomandi svi. Leitast skal vi a koma stofnunum rkisins fyrir ti  landi til jafns vi  hfuborgarsvi  samrmi vi hagsmuni byggarlaga og jar. </p>\n<p>jnusta hins opinbera vi ba hvers byggarlags skal vera sem nst notendum hennar. Stjrn opinberra stofananna skal komi fyrir ti  landi eins og framast er unnt og skynsamlegt getur talist.</p>\n<p>Vi gerum krfu um a komi veri  veg fyrir a strir hlutar lands frist  hendur erlendra aila, t.d. me v a gera bsetu  slandi sem skilyri fyrir eignarhaldi.</p>\n<p>Sameining sveitarflaga skal ekki leia til skertrar jnustu. Tryggja skal a flk njti smu rttinda h bsetu.</p>\n"},{"category":"velferdarmal","name":"Socialno varstvo","statement":"<p>Flokkur flksins krefst ess a ryrkjum og eldri borgurum veri trygg mannsmandi afkoma svo a eir geti lifa me reisn. Stjrnarskrrvarin rttindi essara jflagshpa verur a vira  hvvetna. Lgmarksframfrsla veri 350.000 kr. skatta- og skeringalaust.</p>\n<p>Almannatryggingakerfi er ori allt of flki. rf er  a einfalda kerfi til muna. Vxlverkandi skeringarreglur gera a a verkum a nnast mgulegt er a brjtast t r ftktargildru kerfisins.</p>\n<p>Vi tlum a leggja niur skeringar  ellilfeyri vegna atvinnutekna. Vi tlum a heimila ryrkjum a reyna fyrir sr  vinnumarkai  allt a 2 r n ess a lfeyrir eirra skerist ea rorka eirra veri endurmetin.</p>\n<p>Vi munum hkka frtekjumark ellilfeyris vegna lfeyristekna fr 25.000 kr. upp  100.000 kr.</p>\n<p>Grunnlfeyrir almannatrygginga arf a tryggja afkomu flks og duga til a standa undir grunnframfrslu og hsniskostnai. Flokkur flksins vill a lgmarkstekjutrygging almannatrygginga veri 350.000 kr.  mnui eftir skatt.</p>\n<p>Vi krefjumst ess a Alingi gangi tafarlaust fr lgfestingu  samningi Sameinuu janna um rttindi fatlas flks og algun slenskra laga a honum.</p>\n"},{"category":"husnaedismal","name":"Stanovanjska problematika","statement":""},{"category":"menntamal","name":"olstvo","statement":"<p>Menntun er mannrttindi. Sklastarf  slandi  a vera rttmiki me herslu  jafnri, sjlfsstyrkingu og samskipti ar sem einstaklingurinn fr a njta sn. Flokkur flksins vill stula a bttu menntakerfi  llum stigum og a lsi ungmenna s tryggt. Menntun  slandi veri gjaldfrjls fyrir alla, .m.t. menntun  listum.</p>\n<p>Vi leggjum  herslu  krftugt sklastarf  llum stigum og vill stula a v a nemendum og kennurum li vel  sklanum. Hsklar geti gegnt hlutverki snu me v a tryggja blmlegt rannsknarstarf. Me v er lagur grunnur a framrun, aukinni framleini og bttum lfskjrum  landinu.</p>\n<p>Allir skulu hafa hindraan agang a menntun n tillits til efnahags og bsetu. Mannauinn skal efla me menntun. Vi viljum gjaldfrjlsan agang a sfnum og sningarslum af margvslegum toga sem dpka skilning landsmanna  menningu og sgu jarinnar.</p>\n<p>Til a auka lrislega tttku og skilning flks  mikilvgi ess, arf sklakerfi a veita nemendum strax  grunnskla frslu og jlfun  verki vi lrislega stjrnarhtti og vinnubrg. ll brn eiga rtt  a stunda nm meal jafningja ar sem sjlfsmynd eirra og sjlfstraust fi a roskast og dafna me elilegum htti.</p>\n<p>Fatlair nmsmenn eiga skilyrislaust a geta vali sr nm eftir hugasvii, bi  framhaldsskla og hsklasvii. Agengisml ea stuningur sem  boi er  ekki a vera hindrun  essum efnum. Veita skal frslu um ftlun til nemenda, foreldra og starfsflks menntastofnana.</p>\n"},{"category":"umhverfismal","name":"Varstvo okolja","statement":"<p>Hrein nttra er aulind sem skila ber spilltri til komandi kynsla.</p>\n<p>Flokkur flksins vill nta hreinar orkulindir landsins af skynsemi og  eins sjlfbran htt og unnt er, svo a draga megi r mengun, samhlia nttruvernd. Vira  ll vimi rammatlunar, .e. hrif  nttru; menningu og minjar; samflag og efnahag. Almenningur hafi vallt agang a drri orku.</p>\n<p>Vi styjum ekki takmarkanir  ferafrelsi almennings innanlands til a njta eigin nttru. Hlendi er unasreitur sem m ekki stofnanava  formi jgars me tilheyrandi rherrari  kostna almannarttar. a hafa ekki allir efni  utanlandsferum.</p>\n<p>Vi styjum alla vileitni til a draga r hvers kyns mengun og  Stefnt skal a v a minnka plastnotkun me llum rum, auka endurvinnslu og velja endurnjanlegt hrefni  auknum mli. Stula ber a fullntingu og endurvinnslu, draga r matvlasun og lgmarka urun og brennslu sorps og annars rgangs.</p>\n<p>Vi teljum a a sland eigi a vera fyrirmynd  a a sporna gegn mengun  hafinu. Markvisst jartak veri gert  a hreinsa rusl r fjrum landsins.</p>\n<p>Vi erum hlynnt rannsknum  aulindum  landgrunni slands en leggjumst  gegn olu- og gasvinnslu  hafsvum noran heimskautsbaugs.</p>\n<p>Hafrannsknir skulu efldar me  herslu  vistfrirannsknir.</p>\n"},{"category":"atvinnumal","name":"Zaposlitve","statement":"<p>Flokkur flksins leggur herslu  flugt atvinnulf me tttku sem flestra enda er fjldatttaka landsmanna  atvinnulfiaflvl vermtaskpunar og tekjumyndunar  samflaginu. Vi  viljum a atvinnuvegum jarinnar su bin g skilyri, samt v a  heimilum s trygg rugg afkoma. Heilbrig vermtaskpun er undirstaa velferar.</p>\n<p>Vi styjum nskpun heils hugar og viljum skapa hvetjandi skilyri ess a hugkvmni og dugnaur einstaklinga fi noti sn me framtaki  atvinnulfinu.</p>\n<p>Vi leggjum herslu  rttmikla atvinnustarfsemi um allt land  landbnai, ferajnustu, sjvartvegi, inai og skapandi greinum. Vi fgnum v til dmis a 16% tflutningstekna slands 2019 m rekja til hugverka og skapandi greina. Vi viljum tvfalda  prsentutlu  nsta kjrtmabili me markvissri sknartlun.</p>\n<p>Efla arf fjlbreytni  atvinnuvegumjarinnar, fjlga eggjunum  krfunni. Strauki menntunarstig  a veita okkur spennandi sknarfri  nstu framt innan nskpunar og runar  fjlmrgum svium ar sem gert er t  tflutning. </p>\n<p>Vi leggjum herslu  grna framleislu sem skilar rvalsvrum  breiu svii. Hrein raforka og mynd landsins gefur okkur gott forskot. Vi viljum ekki aeins auka hlutdeild innlendrar framleislu  innanlandsmarkai. Vi teljum einnig a neysluvrur  bor vi slenskt grnmeti eigi fullt erindi  erlenda markai og geti ori a tflutningsvru. </p>\n"},{"category":"heilbrigdismal","name":"Zdravstvo","statement":"<p>Flokkur flksins vill tryggja a heilbrigiskerfi s fjrmagna a fullu svo koma megi  veg fyrir langa bilista eftir nausynlegum agerum og meferum.</p>\n<p>Strauka arf  rri fyrir flk me langvarandi sjkdma og fyrir aldraa, sem veita flki mguleika  v a dveljast heima eins lengi og kostur er. </p>\n<p>Grunnheilbrigisjnusta  a vera gjaldfrjls. a er allt of drt a f alvarlega sjkdma  slandi. Nausynlegt er a draga r kostnaarbyri vegna krabbameinsmefera og mefera vegna annarra alvarlegra sjkdma </p>\n<p>Allir landsmenn skulu hafa sama rtt  heilbrigisjnustu, h bsetu og efnahag. Sama jafnri skal tryggt til tannlkna- og tannrttingajnustu. Vi viljum vinda ofan af eirri run sem hefur tt sr sta undanfarin r og koma  veg fyrir a flk  landsbygginni urfi a ttast svartma neyarjnustu.</p>\n<p> arf a efla geheilbrigisrri til muna. Vi urfum a tryggja a a flk geti leita sr geheilbrigisjnustu n ess a urfa a ba mnuum saman eftir vitlum. Vi eigum ekki a refsa flki fyrir a vera me gesjkdma, heldur hjlpa v.</p>\n<p>Vi hfum  treka tala gegn v a stjrnvld sendi flk  agerir erlendis fremur en  einkareknar lknastvar innanlands me tilheyrandi rttltanlegri sun  almannaf.</p>\n<p>Niurgreia  slfrijnustu til allra og strefla arf slfrijnustu til barna og unglinga.</p>\n"}]

