module.exports = [{"category":"skattamal","name":"Davki","statement":"<p>Miflokkurinn berst gegn hflegri og sanngjarnri skattheimtu og telur a  skattkerfinu su alvarlegar brotalamir sem veri a rst gegn. essar brotalamir stula a mismunun milli jflagshpa og landshluta en ekki sst gagnvart eldri borgurum sem Miflokkurinn horfir srstaklega til enda hefur flokkurinn sett t srstaka rttindaskr sem felur  sr a eldri borgarar munu greia skatt  sama htt og arir.  veri s hluti lfeyrisgreislna sem er fjrmagnstekjur verur skattlagur sem slkur. Lfeyrir og arar hlistar btur fr Tryggingastofnun vera miaar vi umsamin lgmarkslaun og hkki  takti vi launavsitlu. Nverandi hindranir  atvinnu eldra flks vera afnumdar. Frtekjumark atvinnutekna verur hkka  500.000 kr. mnui og frtekjumark greislna r lfeyrissji verur 125.000 kr.  mnui. Me v hvetur kerfi til sparnaar og vermtaskpunar. Miflokkurinn telur a annig er komi  veg fyrir mismunun gagnvart eldri borgurum og tkifri eirra aukin.  vill Miflokkurinn auka vinnuframlag eirra sem hafa vilja og getu til sem san eykur jarframleislu og skatttekjur en btir einnig fjrhag lfeyrissja.</p>\n"},{"category":"jafnrettismal","name":"Enakopravnost spolov","statement":"<p>Miflokkurinn leggur herslu  a allir einstaklingar su jafn rtthir og skuli metnir af eigin verleikum en ekki t fr neinum rum persnulegum einkennum. Miflokkurinn telur mikilvgt a standa vr um frelsi einstaklinga og rtt eirra til a hafa lkar skoanir og tj sig n ess a vera ltnir gjalda fyrir.</p>\n<p>Miflokkurinn tlkar jafnrttisml  vum skilningi og stendur vr um frjlslyndi og  grundvallarhugsjn lris a allir skuli hafa jafnan rtt  a hafa hrif  stjrn samflagsins. Stjrnmlamenn mega v ekki gefa eftir vald til stjrnkerfis sem ekki ber byrg gagnvart kjsendum. annig er mikilvgt a jafnri borgaranna gagnvart rkisvaldinu s tryggt og stofnunum rkisins gert a veita borgurunum jnustu  ekki fugt. Til a n essu fram vill Miflokkurinn tryggja a stjrnvld veiti svr innan afmarkas tma leiti borgarar eftir v.  er mikilvgt a lagalegt og fjrhagslegt jafnrtti s tryggt ef til greinings kemur.  skiptir miklu a almenningi veri veittur sami tmafrestur og jafnri a ru leyti  samskiptum vi stjrnvld. eir sem eru ranglega eru krir ea ltnir sta vingunum af hlfu rkisins munu sjlfkrafa eiga rtt  skaabtum n ess a urfa a fara  dmsml. Miflokkurinn vill a ll gildandi lg su yfirfarin me tilliti til ess hvort au tryggi grundvallarrttindi einstaklinga og su  skiljanleg.  er mikilvgt a dmstlum veri heimilt a lta til ess vi rlausn mla ef lg teljast of skr ea illskiljanleg til ess hgt s a fylgja eim me gu mti. Lykilttur er a jafnri borgaranna gagnvart rkinu fyrir dmstlum verur tryggt.</p>\n"},{"category":"stjornarskrarmal","name":"Epidemilogija","statement":"<p>Miflokkurinn virir stjrnarskr landsins, meal annars me v a treka a ekki su gerar breytingar  stjrnarskr ru vsi en a a s  samrmi vi kvi gildandi stjrnarskrr ar um.</p>\n<p>Miflokkurinn styur endurskoun stjrnarskrrinnar enda hefur slk endurskoun me einum ea rum htti veri  stefnuskr allra rkisstjrna slands fr rinu 2009. Enn sem fyrr virist skorta  samstu meal ings og jar um hverju  a breyta og jafnvel hvernig standa  a breytingum.  hefur veri kvein samstaa um msar endurbtur er varar stjrnarskrna og hefur Miflokkurinn vallt stutt a leita a v sem menn gtu n saman um og rast  breytingar  samrmi vi a. Miflokkurinn leggst gegn byltingarkenndum breytingum  grunnriti stjrnskipunar okkar enda getur slkt skapa rttarvissu. Mikilvgt er a vel s stai a breytingum  eim lgum sem rki setja sr um grundvallarreglur varandi skipulag og stu stjrn rkisins og valdmrk eirra sem fara me rkisvald.</p>\n"},{"category":"evropumal","name":"Evropska integracija","statement":"<p>Miflokkurinn leggur mikla herslu  samvinnu vi lnd Evrpu en hafnar aild a Evrpusambandinu. Miflokkurinn ltur svo  a llum virum vi sambandi hafi veri sliti og veri ekki hafnar aftur nema a undangenginni jaratkvagreislu ar sem jin veri spur hvort hn vill ganga  Evrpusambandi. Miflokkurinn vill endurskoun Schengen samstarfsins og telur a a arfnist endurskounar  ljsi ess a fjlmrg Evrpulnd hafa  undanfrnum rum viki fr grundvallarreglum Schengen fyrirkomulagsins til a verja landamri sn. sland arf a vera frt um a nta stu landsins sem eyja til a hafa stjrn  eigin landamrum en   gu framhaldandi samstarfi vi arar Evrpujir.</p>\n"},{"category":"sjavarutvegsmal","name":"Gospodarstvo","statement":"<p>Innlend matvlaframleisla til sjs og lands er meal grunnstoa samflagsins sem ber a tryggja til framtar. n hennar verur hvorki fu- n matvlaryggi jarinnar tryggt. Mikilvgt er a starfsryggi essara grunnstoa veri tryggt og heildarframlag eirra til samflagsins meti.  sjvartvegi eins og rum greinum urfum vi stefnu sem bitnar ekki  litlu og mealstru fyrirtkjunum ea tir undir samjppun.</p>\n<p>fram skal byggt  aflamarks- og aflahlutdeildarkerfi. Fiskveiar  slenskri lgsgu eru stundaar me sjlfbrum htti eftir rgjf fiskifringa og vottaar af ar til brum viurkenndum ailum. Miflokkurinn styur markmi nverandi fiskveiistjrnunarkerfis og hafnar tilraunum til umbyltinga  v sem haft geta alvarlegar afleiingar fyrir byggir landsins. Miflokkurinn styur srstakan byggakvta til stunings svum sem eiga undir hg a skja.</p>\n<p>Miflokkurinn vill a stjrnarskr kvei  um sameiginlega eign jarinnar  aulindum hennar.  slandi hefur fiskeldi vaxi jafnt og tt, treyst bygg, skapa strf og nnur vermti. Fiskeldi er srstaklega vermt vibt vi atvinnuskpun  landsbygginni. Mikilvgt er a koma  veg fyrir a laxeldi valdi tjni  villtum laxastofnum og ru lfrki, m.a. me krfum um bestu mgulegu tkni, me mtvgisagerum og strngu eftirliti. Fyrirtki veri srstaklega hvtt til a stunda fiskeldi  landi og stuningur vi a veri skoaur.</p>\n"},{"category":"samgongumal","name":"Infrastruktura","statement":"<p>Miflokkurinn horfir til kerfisbreytinga  llum mlaflokkum og a  srstaklega vi  samgngumlum. N nlgun ar mun gjrbylta uppbyggingarhraa samgngumannvirkja. Me aukinni innviafjrfestingu verur hvati til vermtaskpunar straukinn en a tryggir getu samflagsins til a standa undir nausynlegum velferarkerfum. Miflokkurinn vill tryggja a mist innanlandsflugs veri fram  Vatnsmri, styur vi uppbyggingu almenningsamgangna en hafnar Borgarlnu.  leggur Miflokkurinn srstaka herslu  fltingu Sundabrautar.</p>\n<p>Vegakerfi hefur lengi veri vanrkt og arfnast strfelldrar uppbyggingar. Til a auka hraa uppbyggingar arf fyrirkomulag sem tryggir stugar endurbtur og vibtur til framtar. Me stofnun almenningshlutaflags  eigu allra landsmanna getum vi rofi kyrrstuna og sett af sta vivarandi, gagngerar endurbtur  samgngukerfi landsins. Umrddu flagi er tla a  einbeita sr a uppbyggingu stofnvega. Me essu mti getur fari fram fyrirsjanleg og vivarandi vinna ar sem allir stofnvegir vera klrair, einbreium brm trmt af jveginum, feramannaleiir byggar upp, gng lg til a tengja byggir og lagar tvr akreinar  hvora tt ar sem umferarlag er mest.</p>\n"},{"category":"utlendingamal","name":"Migracije","statement":"<p>Mlefni tlendinga verur a skoa undir mrgum sjnarhornum. annig hfum vi innflytjendur sem hinga koma til starfa og auga bi menningu og efnahag. Mikilvgt er a auvelda hingakomu menntas flks sem skilar miklu til slensks atvinnulfs.</p>\n<p>Mlefni hlisleitenda og flttamanna eru vandasamari. Miflokkurinn leggur herslu  a slendingar standi vi snar skuldbindingar gagnvart aljlegum stofnunum en hvetur um lei til ess a horft veri til reynslu ngranalandanna sem vinna n a endurskoun stefnu sinnar.</p>\n<p>Kerfi hlisveitinga er a mrgu leyti  lestri og arfnast gagngerrar endurskounar  bor vi a sem gert hefur veri annars staar  Norurlndum. tkt er a sland veri  auknum mli a fangasta glpagengja sem ffletta hlisleitendur og leggja   strhttu egar ngrannalndin hafa mtt essum veruleika og n rangri.</p>\n<p>Miflokkurinn telur rtt a lta til stefnu danskra jafnaarmanna hva varar endurskoun hlisleitendakerfisins. Rtt eins og hj eim tti markmii a vera a enginn kmi til hinga til a skja um hli. slendingar urfa sjlfir a hafa stjrn  v hverjum yri boi til landsins og beina flki  ruggan og lgmtan farveg. Me hjlp sem nst heimasl mun a fjrmagn sem er til rstfunar ntast sem best til a hjlpa eim sem mest eru hjlpar urfi.</p>\n"},{"category":"sottvarnarmal","name":"Obramba","statement":"<p>Miflokkurinn hefur stutt tillgur sttvarnaryfirvalda  barttunni vi faraldurinn en um lei gagnrnt kvaranir og framkvmd rkisstjrnarinnar  msum eim efnahagslegu agerum sem rist hefur veri . Miflokkurinn rttar a mikilvgt s a tryggja heilsu og ryggi landsmanna me a a markmii a koma samflaginu sem fyrst  skjl og tryggja a lf landsmanna komist sem fyrst aftur  elilegt form.</p>\n"},{"category":"loftslagsmal","name":"Podnebne spremembe","statement":"<p>Miflokkurinn leggur herslu  markvissar og skilvirkar agerir til ess a n raunhfum markmium  loftslagsmlum. Miflokkurinn styur umhverfisvernd bygga  tkni, vsindum og heilbrigri skynsemi. v er mikilvgt a nta umhverfisvna innlenda orku til a auka framleislu  slandi og framleia annig umhverfisvnar afurir, auka tflutningstekjur og hagvxt og bta kjr landsmanna. Velfer verur ekki slitin r samhengi vi vermtaskpun. Miflokkurinn telur a unnt s a n llum markmium  loftslagsmlum n ess a skera efnahag landsmanna og mikilvgt er a tryggja a agerir hitti ekki fyrir  sem standa hllum fti efnahagslega.</p>\n<p>Eftirspurn eftir endurnjanlegri orku veitir slendingum mld tkifri til a auka vermtaskpun um allt land. Vi getum auki orkuframleislu, btt flutningskerfi og ntt virkjanir og orkukosti betur. Miflokkurinn telur a slendingar ttu a einsetja sr a flytja t a minnsta kosti eins miki eldsneyti og flutt er inn. Til dmis mtti framleia vetni og binda a, ammonak og metenl. Heimurinn arf miklu meiri endurnjanlega orku. arna getur sland komi me mikilsver framlg og arf a nta tkifrin betur, jafnvel framleia meira. a getur tt a losun landsins aukist fremur en a minnka en  heimsvsu mun a draga r losun og vera a besta sem vi getum gert  loftslagsmlum. a mun einnig bta lfskjr slendinga.</p>\n"},{"category":"byggdarmal","name":"Pokrajine","statement":"<p>Kjarninn  byggastefnu Miflokksins birtist  slagorinu sland allt, sem gengur t  a innleia heildarsn  byggamlum ar sem jafnrtti allra, h bsetu, verur tryggt. Byggastefnan sland allt hefur aldrei veri eins akallandi og n. Aeins  ann htt getum vi fari r drri vrn  arbra skn fyrir landi allt.</p>\n<p> sta ess a reyna smskammtalkningar hr og ar er mikilvgt a allar agerir tengist saman og eitt styji anna. slandi allt er tla a n til allra tta sem skipta bygg  landinu miklu, svo sem  samgngur, fjarskipti, orkuflutninga, heilbrigisjnustu, menntaml, astum atvinnurekenda og allri annari eirri jnustu sem allir landsmenn eiga jafnan rtt .</p>\n<p>Miflokkurinn hefur hyggur af misvgi milli landsbyggarinnar og suvesturhornsins og telur brnt a styja vi uppbyggingu ti  landi og efla annig bygg um allt land. essi sn nr til allra mlaflokka en brnast er a tryggja a velferakerfi okkar slendinga gagnist llum. v hefur  Miflokkurinn lagt mikla herslu  a styrkja heilbrigisjnustu  landsbygginni og sna annig vi eirri heillarun sem hefur ori ar undanfarna ratugi me sversnandi jnustu og verra agengi a henni. Miflokkurinn hyggst styrkja  n heilsugslujnustu og srfrilkningar utan hfuborgarsvisins.  sama htt er mikilvgt a llum landsmnnum standi til boa jafng menntun  skyldustigi en v miur er mikill misbrestur ar .  arf ekki a taka fram a samgnguml eru byggaml.</p>\n"},{"category":"velferdarmal","name":"Socialno varstvo","statement":"<p> velferarmlum leggur Miflokkurinn srstaka herslu  a koma til mts vi arfir eldri borgara en s hpur getur ekki bei lengur eftir rttlti. Miflokkurinn leggur herslu  a nverandi fyrirkomulag skeringa veri afnumi og ess  sta komi sanngjarnt kerfi ar sem vistarf flks s virt og komi  jkvum hvtum sem stula a vinnu, vermtaskpun og sparnai. eir sem vilja vinna lengur eiga a hafa rtt  v. a er rttltt og hagkvmt fyrir rkissj, lfeyriskerfi og samflagi allt.</p>\n<p> mlefnum ryrkja vil Miflokkurinn horfa til smu vimia um sanngirni, elilegt lfsviurvri og jkv hvata.</p>\n"},{"category":"husnaedismal","name":"Stanovanjska problematika","statement":"<p>Miflokkurinn vill rast  heildstar agerir  eim tilgangi a gera marka me bir skilvirkari og sveigjanlegri annig a hann geti komi til mts vi arfir almennings. Um lei  stefna stjrnvalda a stula a v a byggt veri fjlbreytt gahsni og a laskortur hamli ekki nausynlegri uppbyggingu.  sama tma  arf a vera tryggt a llum slenskum rkisborgurum standi til boa tkifri til a eignast eigi hsni. Allir sem  urfa a halda eigi rtt  mtframlagi rkisins sem svo verur greitt til baka vi slu fasteignar ea breytt  ln a 10 rum linum.</p>\n<p>Miflokkurinn leggur srstaka herslu  a samykkt vera lg um eignarrttarstefnu  hsnismlum. Tryggja arf hvetjandi regluverk  bamarkai og stula a fjlbreyttara og manneskjulegra hsni. Um lei arf a huga a v a skattar og gjld trufli ekki markainn og Miflokkurinn vill afnema stimpilgjld. Einnig leggur Miflokkurinn til a almenningur fi heimild til a setja 3,5 prsentustig af lfeyrisigjaldi snu  sj sem nta m til eigin fasteignakaupa.</p>\n"},{"category":"menntamal","name":"olstvo","statement":"<p>Miflokkurinn leggur herslu  a menntakerfi endurspegla rf jarinnar fyrir aukna vermtaskpun og framrun  nstu rum og ratugum. Fjrmagn veri v srstaklega beint   nm  tkni- og ingreinum og stutt vi  sem vilja stunda slkt nm. Um lei arf a tryggja jafna stu allra nemenda, hvar sem er  landinu.</p>\n<p>Atvinnulfi arf  v a halda a menntakerfi tskrifi flk me ekkingu  eim svium sem skipta mestu mli vi efnahagslega uppbyggingu landsins. a hefur lengi veri rtt um mikilvgi ess a efla in- og tkninm.  mrgum tilvikum er slkt nm drara en anna nm. Fjrveitingarnar urfa a taka mi af v og stjrnvld feimin vi a eyrnamerkja fjrveitingar kvenu nmi. Skattgreiendur, einstaklingar og fyrirtki borga reikninginn. eir eiga a geta haft hrif  hvernig fjrmagni er ntt. Slk nlgun auveldar lka rkisvaldinu a lika fyrir samstarfi skla og fyrirtkja og auka skn  nmi. rlega ljka 700 drengir grunnsklanmi n ess a vera okkalega lsir. a er einnig slandi stareynd a ungir menn eru aeins 30% eirra sem ljka meistaragru r hsklanum og karlmenn eru n einungis rijungur eirra sem stunda hsklanm. Miflokkurinn leggur herslu  a bta stu drengja  sklakerfinu me v a horfa til arfa eirra.</p>\n"},{"category":"umhverfismal","name":"Varstvo okolja","statement":"<p>Miflokkurinn leggur herslu  a nta gi landsins me sjlfbrni og hreinleika sem markmi. annig veri gert tak  mehndlun rgangs og frveituml efld annig a au smi matvlaframleisluj. Miflokkurinn leggur herslu  a a er umhverfisvnna a framleia matvli nlgt heimamarkai  sta ess a flytja au um langan veg. Aukin vinnsla  endurnjanlegri, grnni orku skapar tal tkifri  matvlaframleislu sem geti  framtinni starfa  htkniumhverfi hr  landi. Hr eru kjrastur fyrir sjlfbra matvlaframleislu.</p>\n<p>Um lei er mikilvgt a gerbreyta stefnu slendinga  sorpmlum en Miflokkurinn hefur tala fyrir v a hr veri reist htknisorpbrennslust. Hinga til hefur langmest af v sorpi sem fellur til  veri ura ea sent til tlanda og ljst er hva verur um a ar. a er landi. Miflokkurinn telur brnt a knnu veri hagkvmni ess a vi slendingar sjum um okkar sorp sjlf og httum urun, endurvinnum ess  sta eins og unnt er og brennum a sem t af stendur  eins umhverfisvnan htt og unnt er.</p>\n<p>Rki heimsins leita n a grnum hagvexti. Miflokksmenn styja skynsamar lausnir sem  senn auka velfer og lfsgi og draga r grurhsalofttegundum. a hefur gefist mannkyninu best til essa. Miflokkurinn leggur herslu  a verndun og stjrnssla umhverfisml s sem mest hj  heimamnnum og hefur v lagst  gegn stofnun hlendisjgars.</p>\n"},{"category":"atvinnumal","name":"Zaposlitve","statement":"<p>Miflokkurinn telur mikilvgt a rast  kerfisbreytingar sem skili aukinni skilvirkni og framleini hj hinu opinbera. v er nausynlegt  a minnka rkisbkni, einfalda regluverk og draga r kerfisri  slandi. annig er hgt a draga r lgum  almenning og ltil og mealstr fyrirtki f auki frelsi til a skapa n strf og n vermti. Stjrnvld eiga a jnusta almenning, ekki fugt.</p>\n<p>Miflokkurinn leggur herslu  a rekstrarstaa og samkeppnisstaa ltilla og mealstrra fyrirtkja veri btt me skattalegum hvtum og einfaldara regluverki annig a  ekki su lagar smu kvair  ltil fyrirtkin og au strri.</p>\n<p>Miflokkurinn leggur herslu  a stjrnvld megi ekki  innleia ntt yngjandi regluverk nema tvfalt fleiri yngjandi reglur veri afnumdar samtmis  samrmi vi stefnu rkisstjrnar ranna 2013-2016.</p>\n"},{"category":"heilbrigdismal","name":"Zdravstvo","statement":"<p>Tryggja ber llum slendingum jafnt agengi a gri heilbrigisjnustu.  v skyni veri horfi fr samjppunarstefnu  heilbrigismlum og jnustan fr nr almenningi um allt land. Heilbrigiskerfi arfnast endurskounar til a tryggja sem besta ntingu fjrmagns me a a markmii a hmarka jnustu vi sjklinga. Rki arf v a nta jnustu eirra sem eru best til ess fallnir a veita lkningar  viunandi kjrum, en einnig krafta ggerarsamtaka og sjlfstra flaga, sem hafa skipt skpum vi a astoa sjklinga og bta lheilsu og gera a enn.</p>\n<p>Me v a eya bilistum, fjlga hjkrunar- og jnusturmum og efla heimajnustu m draga r arfa kostnai  heilbrigiskerfinu og bta lf sjklinga og eldri borgara  slandi. a er mikilvgt a jnusta vi sjklinga veri bygg upp um allt land, nverandi heilbrigisstofnanir veri betur nttar og njar reistar. Meal annars veri hafinn undirbningur a ntmalegu sjkrahsi  njum sta  hfuborgarsvinu.</p>\n<p>Miflokkurinn hefur lagt srstaka herslu  a llum slenskum rkisborgurum, bsettum  slandi, 40 ra og eldri, standi til boa almenn heilbrigisskimun  riggja ra fresti eim a kostnaarlausu. a mun bjarga mannslfum, bta lheilsu og spara peninga fyrir heilbrigiskerfi til lengri tma liti.</p>\n"}]

