module.exports = [{"category":"skattamal","name":"Davki","statement":"<p>Ef g tti a lsa skattastefnu Prata  tveimur orum  vri a framskin&quot; <em>(e. progressive) </em>og grn.&quot; a ir a litli maurinn beri ekki ungar byrar og a vi verlaunum a sem er umhverfisvnt.</p>\n<p><strong>Vi viljum t.d. lkka skatta :</strong> <br/>\nLg laun <br/>\nrorku- og ellilfeyrisega <br/>\nUmhverfisvna vrur og jnustu <br/>\nGrn sprotafyrirtki <br/>\nLtil, loftslagsvn fyrirtki</p>\n<p><strong>Vi viljum hkka skatta :</strong> <br/>\nH laun <br/>\nOfurau <br/>\nAr og fjrmagnstekjur <br/>\nHagnaardrifna aulindantingu eins og sjvartveg og striju <br/>\nMengun og umhverfisvn strfyrirtki</p>\n<p> viljum vi a sveitarflg um land allt fi strri hlut af vermtunum sem au skapa, eins og t.d. af virisaukaskatti, gistinttagjaldi ea tekjuskatti fyrirtkja. annig myndast rmi og fjrmagnfyrir fjlbreytta atvinnu um land allt.</p>\n<p>Til framtar viljum vi lka a skattkerfi veri miklu kvikara. A skattar veri gerir upp sjlfkrafa og ll kerfi virki  rauntma. annig mtti t.d. skattleggja mengun um lei og hn  sr sta.</p>\n<p>Nnari upplsingar <a href=\"https://piratar.is/kosningastefna/\">m nlgast  kosningastefnu Prata</a></p>\n"},{"category":"jafnrettismal","name":"Enakopravnost spolov","statement":"<p>Jafnrtti allra er Prtum grarlega mikilvgt. a sem meira er, jafnrtti er einn af hornsteinum <a href=\"https://piratar.is/stefnumal/grunnstefna/\">sjlfrar grunnstefnu Prata</a> sem allar arar stefnur okkar byggja .  grunnstefnunni okkar segir meal annars:</p>\n<ul>\n<li><em>Pratar beita sr fyrir eflingu og verndun borgararttinda.</em></li>\n<li><em>tvkkun borgararttinda skal mia a styrkingu annarra rttinda.</em></li>\n<li><em>Standa arf vr um nverandi rttindi og gta ess a au su ekki skert.</em></li>\n<li><em>Pratar telja a borgararttindi tilheyri einstaklingum, og a rttur hvers og eins s jafn sterkur.</em></li>\n</ul>\n<p>Vi stofnun flokksins settum vi lka <a href=\"https://x.piratar.is/polity/1/issue/85/\">srstaka jafnrttisstefnu</a>.  henni kemur fram a:</p>\n<ul>\n<li><em>Margt s enn unni til a jafna stu lkra hpa samflagsins; .m.t. kynjanna, barna, aldrara, hinsegin flks, innflytjenda og fatlara</em></li>\n<li><em>Pratar berjist gegn mismunun og staalmyndum um flk</em></li>\n<li><em>Ekki s ng a samflagi s umburarlynt heldur s mikilvgt a allir einstaklingar su samykktir, metnir a verleikum og fi jfn tkifri til a njta sn, vaxa og dafna</em></li>\n<li><em>Ofbeldi skuli aldrei last hvorki andlegt n lkamlegt, og huga arf srstaklega a stu lkra hpa og sna frslu, forvarnir og meferarrri a lkum astum flks</em></li>\n<li><em>Hvetja skuli til opinnar og upplstrar umru um jafnrttisml</em></li>\n</ul>\n<p> eru Pratar jafnframt me stefnu <a href=\"https://x.piratar.is/polity/1/document/49/\">gegn kynbundnu ofbeldi  tta lium</a>.</p>\n"},{"category":"stjornarskrarmal","name":"Epidemilogija","statement":"<p>Stefna Prata  stjrnarskrrmlum er mjg einfld:</p>\n<p>Vi viljum a Alingi innleii nja stjrnarskr sem grundvallast  tillgum stjrnlagars  nsta kjrtmabili, lkt og jin kva  um ann 20. oktber 2012. Alingi  a vira vilja jarinnar og efna lofori fr lveldisstofnun um a slenska jin fi nja stjrnarskr.</p>\n<p><strong>Klrum stjrnarskrna  kjrtmabilinu</strong> <br/>\nVi viljum a Alingi samykki nju stjrnarskrna  kjrtmabilinu og efni til jaratkvagreislu  kjlfari, samhlia ar nstu alingiskosningum. Nja stjrnarskrin tekur af rkjandi vafa um valdmrk og byrg valdhafa, festir ntmaleg mannrttindi  sessi, tryggir nttruaulindir  jareign og eflir lri, nttruvernd og gagnsja stjrnsslu n ess a kollvarpa stjrnskipan landsins.</p>\n<p><strong>Rkt almenningssamr og vndu vinnubrg</strong> <br/>\nPratar vilja a ll umra um mguleg frvik fr tillgum stjrnlagars fari fram  vtku og opnu samri vi almenning. Tillgur stjrnlagars eiga a vera tgangspunkturinn og breytingar eiga a n markmium ea anda eirra. Vi teljum farslast a Alingi taki nju stjrnarskrna og einstk kvi hennar til umru  hverju ri nsta kjrtmabils, til a varpa ljsi  einstk efnisleg greiningsatrii og veita llum  ingi svigrm til a tj skoanir snar  mlinu.</p>\n"},{"category":"evropumal","name":"Evropska integracija","statement":"<p><strong>EES</strong> <br/>\nPrtum finnst EES-samningurinn hafa veri miki heillaspor fyrir jina. ess vegna er framhaldandi EES-samstarf  dagskr hj Prtum og a sem meira er - sland myndi taka sr allt a rmi sem a gti  vettvangi EES til a tryggja enn betur stu og hagsmuni almennings.</p>\n<p><strong>Evrpusambandi</strong> <br/>\nPrtum finnast aildarvirur vi Evrpusambandi vara heildarhagsmuni allrar jarinnar og v er mikilvgt a stjrnvld fylgi vilja almennings  eim efnum. Pratar munu ess vegna hvorki hefja aildarvirur, n ljka eim, n jaratkvagreislu.  adraganda beggja atkvagreisla gerum vi krfu um a fram fari hlutlaus og heildst kynning  kostum og gllum aildar, svo a almenningur eigi auveldara me a taka upplsta kvrun  kjrklefanum.</p>\n<p><strong>Anna Evrpusamstarf</strong> <br/>\nPrtum finnst a sland eigi a gerast aili a Evrpsku geimvsindastofnuninni (ESA) og Kjarnrannsknastofnun Evrpu (CERN). a myndi opna margvslegar gttir fyrir slenska vsindamenn.  Evrpumlum, eins og utanrkismlum almennt, leggja Pratar jafnframt mikla herslu  loftslagsml, mannrttindi (t.a.m. eftirlit me mannrttindabrotum  frverslunarsamningum) og gagnsi - ekki sst vi samningager, eins og sst af formennsku ingmanna Prata  laga- og mannrttindanefnd Evrpursingsins og ingmannanefndar EFTA-rkjanna.</p>\n"},{"category":"sjavarutvegsmal","name":"Gospodarstvo","statement":"<p>Pratar telja a sjvaraulindin s sameiginleg og varandi eign slensku jarinnar. Fiskurinn  sjnum tilheyrir okkur llum og slenska jin  a f almennilegt gjald aulindina. Pratar tla a rast  rttkar breytingar  sjvartvegskerfinu me uppboi  aflaheimildum og nliun  sjvartvegi.</p>\n<p><strong>Eignarhald  aulindinni</strong> <br/>\nPratar vilja a stjrnarskrin kvei  um a jin s eigandi sjvaraulindarinnar. a er hgt me v a <a href=\"http://stjornlagarad.is/starfid/frumvarp/\">innleia 34. grein nju stjrnarskrrinnar.</a></p>\n<p><strong>Tmabundin ntingarleyfi &amp; aulindarentan til jarinnar</strong> <br/>\nPratar vilja a rki bji upp aflaheimildir til leigu  opnum markai. Aflaheimildir veri tmabundnar og leigugjald eirra renni a fullu til jarinnar Til a koma  veg fyrir samjppun tla Pratar a hera kvtaaki og efla Fiskistofu og Samkeppniseftirliti</p>\n<p><strong>Frjlsar handfraveiar</strong> <br/>\nPratar vilja frjlsar handfraveiar. Fyrsti fanginn verur a tryggja llum handfrabtum me fjrar rllur 48 daga  hverri strandveiivert n stvunarheimildar.\\</p>\n<p><strong>Allur afli  marka</strong> <br/>\nPratar vilja a allur afli fari upphaflega  gegnum innlendan fiskmarka til a tryggja elilegt markasver  llu sjvarfangi.</p>\n<p><strong>Vsindi og gagnsi</strong> <br/>\nPratar vilja koma  veg fyrir plitsk afskipti af rannsknum, veiirgjf og eftirliti.  viljum vi efla eftirlitsstofnanir og gera allar upplsingar um sjvartveg opinberar, auk ess a leggja niur verlagsstofu skiptavers.</p>\n<p><a href=\"https://x.piratar.is/polity/1/issue/473/\">Sjvartvegsstefnu Prata m nlgast hr</a></p>\n"},{"category":"samgongumal","name":"Infrastruktura","statement":"<p>Sn Prata  samgngur gengur t  tvennt: A flk hafi frelsi til a velja sr ann samgngumta sem a vill og a flta orkuskiptum  samgngum.</p>\n<p><strong>Valfrelsi  samgngum</strong> <br/>\nUndanfarna ratugi hafa stjrnvld lagt herslu  a slendingar urfi a eiga bl og <a href=\"https://www.fib.is/static/files/rekstrarkostnadur/rekstur-bifreida-2021-pdf.pdf\">borga rma milljn  ri fyrir viki</a>. Rannsknir hafa snt a sfellt fleiri <a href=\"https://maskina.is/vilji-til-breyttra-ferdavenja/\">vilji ferast me rum htti</a> og eya milljninni  eitthva anna. Vi viljum svara essari eftirspurn og auvelda flki a ferast eftir snu hfi. Fyrsta skref er a skilgreina almenningssamgngur og virka feramta sem hluta af grunnneti samgangna.  vri jr a leggja hjlasla mefram hringveginum. Hjlaferamennska mun aukast og hjlasli strbtir ryggi allra vegfarenda.</p>\n<p><strong>Orkuskipt</strong> <br/>\nPratar vilja auvelda orkuskiptin me v a byggja upp nausynlega innvii um allt land og skapa hvata fyrir flk til a nta sr vistvna feramta.  viljum vi styja nskpun  sjlfbrum orkugjfum; hvort sem um rir rafmagn, vetni, metan ea ara nja tkni.</p>\n<p><strong>Skipulag samgangna</strong> <br/>\nSkilvirkar og umhverfisvnar samgngur urfa fluga og fullfjrmagnaa samgngutlun, ekki ljs lofor  blai. Pratar vilja leyfa bum hvers landssvis a forgangsraa framkvmdum  samgngutlun,  samrmi vi <a href=\"https://piratar.is/kosningastefna/\">byggastefnu Prata</a>. Prata<a href=\"https://x.piratar.is/polity/124/document/424/?v=1\">r hafna innheimtu vegtolla</a> til a fjrmagna almennar samgnguframkvmdir.</p>\n"},{"category":"utlendingamal","name":"Migracije","statement":"<p>Pratar tala fyrir nrri nlgun  mlefnum innflytjenda  slandi. Nstum fimmti hver einstaklingur  vinnumarkanum er af erlendu bergi brotinn - sland myndi hreinlega ekki ganga n tlendinga. a er v brn rf  margvslegum breytingum  tlendingamlum.</p>\n<p><strong>Leggjum niur tlendingastofnun</strong> <br/>\nPratar vilja leggja niur tlendingastofnun  nverandi mynd og fela verkefni stofnunarinnar rum embttum, svo sem jskr og sslumnnum.</p>\n<p><strong>Gerum etta betur</strong> <br/>\nPratar vilja leggja herslu  mannlegri, einfaldari og notendavnni mehndlun umskna um dvalarleyfi og rkisborgarartt. Umsknir flks eiga almennt a vera teknar til efnismeferar og  llum tilvikum skal skilgreina mlsmeferartma fr upphafi umsknar og fram a v a einstaklingur er fluttur r landi.</p>\n<p><strong>Brottvsanir</strong> <br/>\nPratar telja brottvsanir eirra sem hafa alagast hr  landi, srstaklega barna, mannlegar og a eim skuli htt n tafar. A sama skapi eru brottvsanir til ruggra rkja innan Evrpu, ar me tali Grikklands og Ungverjalands, landi og eim ber a htta strax.</p>\n<p><strong>Arar agerir</strong> <br/>\nPratar vilja:</p>\n<ul>\n<li>Styja vi frnarlmb mansals</li>\n<li>Styja erlenda verkamenn og refsa brotlegum vinnuveitendum</li>\n<li>Efla slensku- og murmlskennslu barna</li>\n<li>Efla upplsingagjf til tlendinga um rttindi eirra</li>\n<li>A llum tegundum dvalarleyfis fylgi atvinnuleyfi</li>\n<li>Hjlpa tlendingum a alagast og grpa til agera gegn tlendingahatri</li>\n<li><a href=\"https://x.piratar.is/polity/1/issue/474/\">Og fleira</a></li>\n</ul>\n"},{"category":"sottvarnarmal","name":"Obramba","statement":"<p><a href=\"https://piratar.is/stefnumal/grunnstefna/\">Grunnstefna Prata</a> hefur reynst gott leiarljs  faraldrinum.  grunnstefnunni er rennt sem kom srstaklega a gum notum  fordmalausum tmum:</p>\n<ul>\n<li><em>Pratar leggja herslu  gagnrna hugsun og vel upplstar kvaranir.</em></li>\n<li><em>Pratar beita sr fyrir eflingu og verndun borgararttinda.</em></li>\n<li><em>Pratar telja gagnsi eiga mikilvgan tt  a almenningur s upplstur og ar af leiandi hfur til lrislegrar kvrunartku.</em></li>\n</ul>\n<p>Grunnstefna Prata, sem allar arar stefnur okkar byggja , hafi v au hrif  faraldrinum a:</p>\n<ul>\n<li><em>Vi vildum a vsindin vru  fyrirrmi,  samrmi vi krfuna um vel upplstar kvaranir.</em></li>\n<li><em>Vi lgum herslu  a ekki yri gengi lengra  frelsi flks en brn nausyn krefist,  samrmi vi verndun borgararttinda.</em></li>\n<li><em>Vi klluum stugt eftir llum ggnum og rkum fr stjrnvldum,  samrmi vi krfuna um a almenningur gti veri upplstur um stuna og forsendur agera.</em></li>\n</ul>\n<p>essa dagana er grasrt Prata a greia atkvi um srstaka sttvarnastefnu fyrir flokkinn til framtar. egar niursturnar liggja fyrir munum vi uppfra etta svar.</p>\n"},{"category":"loftslagsmal","name":"Podnebne spremembe","statement":"<p>Pratar hafa sett sr metnaarfulla loftslagsstefnu sem allar arar stefnur flokksins hvla . Nskpun, samgngur, bygga- ea efnahagsml - alls staar eru grnar agerir  fyrirrmi. Tkifrin  loftslagsvnum lausnum eru teljandi og munu vara leiina  atvinnu- og vermtaskpun nstu ratugina. Pratar vilja grpa essi tkifri.</p>\n<p>Loftslagsstefna Prata er  tta kflum, sem hlja svona  mjg grfum drttum:</p>\n<ol>\n<li><strong>Skr stefna  tt a kolefnishlutleysi</strong> <br/>\nSetjum metnaarfull markmi og stndum vi au</li>\n<li><strong>Valdeflum almenning</strong> <br/>\nAuveldum flki a taka tt  breytingunum</li>\n<li><strong>Grn umbreyting  allra hag</strong> <br/>\nGerum krfur  stjrnvld og fyrirtki sem bera mesta byrg</li>\n<li><strong>Stjrnssla og stjrnvld</strong> <br/>\nStyrkjum stjrnsslu loftslagsmla</li>\n<li><strong>Grn umbreyting atvinnulfs</strong> <br/>\nStyrkjum grna sprota og bum til grna hvata</li>\n<li><strong>Nttruvernd</strong> <br/>\nVerndum spillta nttru og setjum vernd mihlendis  lrislegt ferli</li>\n<li><strong>Hringrsarsamflag</strong> <br/>\nSetjum skra stefnu  hringrsarsamflag</li>\n<li><strong>Agerir  aljasviinu</strong> <br/>\nsland beiti sr fyrir loftslagsmlum  aljavettvangi</li>\n</ol>\n<p>Loftslagsstefna Prata er rttk, tarleg og metnaarfull. <a href=\"https://piratar.is/frettir/loftslagsstefna-pirata/\">Hana m nlgast  heild hr</a>.</p>\n"},{"category":"byggdarmal","name":"Pokrajine","statement":"<p>Allar stefnur <a href=\"https://piratar.is/kosningastefna/\">Prata eru  raun byggastefna</a>. Vi viljum sjlfbr sveitarflg, me gu agengi a grunnjnustu og skapa astur fyrir aukna nskpun, vermtaskpun og framleislu  heimahgum. Vi viljum jafnframt fra kvaranatku  meira mli til nrsamflagsins.</p>\n<p><a href=\"https://www.visir.is/g/20212145006d/voldin-heim-i-herad\"><strong>Vldin heim  hra</strong></a> <br/>\nPratar tra v a flk  svinu viti, rum fremur, hva s svinu fyrir bestu. ess vegna viljum vi draga r mistringu rkisins og gera bum landsbyggarinnar kleift a mta umhverfi sitt sjlf.</p>\n<p><a href=\"https://www.visir.is/g/20212144784d/hvar-munu-milljon-islendingar-bua-\"><strong>Sveitarflg njti vermta sem au skapa</strong></a> <br/>\nPratar vilja a sveitarflg fi meira af tekjunum sem au skapa, t.d. af virisaukaskatti, fjrmagnstekjuskatti og tekjuskatti fyrirtkja. Frjlsar handfraveiar, sem vi berjumst fyrir, munu styrkja brothttar byggir sem og <a href=\"https://piratar.is/kosningastefna/\">nskpunarstefna Prata.</a></p>\n<p><strong>Sterkir innviir um land allt</strong> <br/>\nPratar vilja framrun um land allt og til ess arf ga innvii - eins og hhraa internet, traust rafmagn og ruggar samgngur.</p>\n<p><strong>Agengi a jnustu</strong> <br/>\nPratar telja a bseta eigi ekki a koma  veg fyrir agengi flks a nausynlegri jnustu. kvaranir okkar  essum efnum munu alltaf hvla  eirri sn.</p>\n<p><strong>Valdi til flksins</strong> <br/>\nPratar vilja auvelda flki a hafa hrif  sveitarflgin sn - t.d. me v a auka gagnsi  stjrnsslu sveitarflaga og auvelda bum a boa til bakosninga.</p>\n"},{"category":"velferdarmal","name":"Socialno varstvo","statement":"<p>Pratar tla a fjrfesta  flki og skapa annig efnahagslegan sveigjanleika fyrir flk til a dafna  eigin forsendum. Pratar vita a velfer hvlir  mrgum stoum; eins og heilbrigu efnahagskerfi, heilnmu hsni og umhverfi, gu heilbrigis- og menntakerfi og sast en ekki sst: Stuningskerfum sem virka. Hr a ofan eru stefnur Prata  mrgum essara mla, en hr segi g rstutt fr stefnum okkar  mlefnum eldra flks og ryrkja.</p>\n<p><strong>Mlefni eldra flks</strong> <br/>\nEldra flk br vi sanngjarnt lfeyriskerfi og fullngjandi jnustu. Vi viljum koma fram vi fullori flk eins og fullori flk, a fi a ra snu eigin lfi en s ekki fast  boum, bnnum og skeringum. Vi viljum tryggja mannsmandi framfrslu eldra flks, viunandi heilbrigis- og velferarjnustu og hsnisryggi. Meginstef Prata  mlefnum eldra flks eru velsld, ryggi, samr og viring.</p>\n<p><strong>ryrkjar og fatla flk</strong> <br/>\nPratar tra a ll eigi skili smu tkifri og viljum endurhanna kerfin okkar annig a au valdefli einstaklinga. Vtkar skeringar og hindranir  nverandi kerfum skapa samflagslegt tap og frna tkifrinu til a fjrfesta  einstaklingnum. Tryggjum rtt allra sem urfa til framfrslu vegna rorku og endurhfingar annig a hn dugi til mannsmandi framfrslu og vieigandi bsetu.</p>\n<p><a href=\"https://piratar.is/kosningastefna/\">Nnari upplsingar eru hr.</a></p>\n"},{"category":"husnaedismal","name":"Stanovanjska problematika","statement":"<p>Pratar telja a ruggt hsaskjl s ein af grunnrfum mannsins. v viljum vi a stjrnvld beiti sr af krafti  hsnismlum og sji til ess a landsmenn hafi ak yfir hfui.  stuttu mli viljum vi endurhanna hsniskerfi, enda eru hsnisml eitt strsta kjaramli. Fra kerfi fr grabraski  tt a hagnaardrifnum hsnisflgum.</p>\n<p>Hsnisstefna Prata er  sex kflum, sem hlja svona  mjg grfum drttum:</p>\n<ol>\n<li><strong>Komum hsnismarkai  jafnvgi me strauknum framlgum til nrra ba</strong> <br/>\nTryggjum frambo af bum samkvmt fyrirsri rf og herjum  uppsafnaan skort</li>\n<li><strong>Hsni fyrir nmsflk</strong> <br/>\nGerum heimavist a raunverulegum valkosti um allt land</li>\n<li><strong>Bseturri fyrir ll sem urfa</strong> <br/>\nFjlgum bum sem koma til mts vi arfir lkra hpa,  takti vi eirra skir</li>\n<li><strong>Eflum rttindavernd leigjenda</strong> <br/>\nEflum rttindi leigjenda, styrkjum leigjendasamtk og stulum a langtmaleigusamningum</li>\n<li><strong>Skylduhlutfall flagsba  sveitarflgum</strong> <br/>\nSkilyrum lgmarkshlutfall flagsba  sveitarflgum yfir tiltekinni str</li>\n<li><strong>Heilnmt hsni</strong> <br/>\nAgerir sem stula a heilnmu hsni, gagnagrunnur um stand fasteigna og tak gegn rakaskemmdum og myglu</li>\n</ol>\n<p>Hsnisstefnu Prata m <a href=\"https://piratar.is/kosningastefna/\">nlgast  heild hr.</a></p>\n"},{"category":"menntamal","name":"olstvo","statement":"<p>Framundan eru miklar samflagslegar breytingar sem menntakerfi arf a ba nemendur undir. Pratar tla a auka sveigjanleika  menntakerfinu, setja nemandann  forgrunn, styja vi kennara og auka herslu  frni sem ntist  sjlfvirknivddu samflagi  upplsingald.</p>\n<p>Menntastefna Prata er  11 kflum, sem hlja svona  mjg grfum drttum:</p>\n<ol>\n<li><strong>Gagnrnin hugsun og upplsingalsi</strong> <br/>\nhersla  gagnrna hugsun og lsi  vum skilningi</li>\n<li><strong>Aukinn sveigjanleiki</strong> <br/>\nSveigjanleg skil milli sklastiga og aukin s- og endurmenntun</li>\n<li><strong>Nemandinn  forgrunn</strong> <br/>\nNemandinn veri mipunktur menntakerfisins</li>\n<li><strong>Frri prf og meira smat</strong> <br/>\nAukin hersla  smat, reynslumia nm og reglulega endurgjf</li>\n<li><strong>Frjlst, opi og lrislegt menntakerfi</strong> <br/>\nNemendur og kennarar fi a mta kennsluna innan vs ramma.</li>\n<li><strong>Stuningur vi starfsflk</strong> <br/>\nMetum kennara a verleikum og styrkjum starfssamflg eirra</li>\n<li><strong>Uppfrum menntakerfi</strong> <br/>\nEndurskoum nmskr  breiu samri fyrir breytingarnar sem framundan eru</li>\n<li><strong>Framfrsla nemenda</strong> <br/>\nFrum okkur r nmslnum yfir  styrki og greium t persnuafsltt</li>\n<li><strong>ryggi fr ofbeldi og reitni</strong> <br/>\nTryggjum verkferla og tlanir sem stula a ryggi barna og nmsmanna</li>\n<li><strong>Brum bili milli fingarorlofs og leikskla</strong> <br/>\nRki og sveitarflg vinni a v a lta leiksklaplss standa til boa eftir fingarorlof</li>\n<li><strong>Metum menntunina</strong> <br/>\nViurkennum menntun og starfsrttindi tlendinga</li>\n</ol>\n<p>Menntastefnu Prata <a href=\"https://piratar.is/kosningastefna/\">m nlgast  heild hr</a>.</p>\n"},{"category":"umhverfismal","name":"Varstvo okolja","statement":"<p>Stefna Prata  umhverfismlum er <a href=\"https://piratar.is/frettir/loftslagsstefna-pirata/\">rjfanlegur hluti af loftslagsstefnu flokksins.</a> ar er srstaklega tala um hringrsarhagkerfi og nttruvernd, enda viljum vi a spillt nttra eigi sterkan mlsvara vi stjrnvlinn.  er Prtum annt um verndun hafsins, sem sst  <a href=\"https://x.piratar.is/polity/1/issue/473/\">sjvartvegs-</a> og <a href=\"https://piratar.is/kosningastefna/\">fiskeldisstefnunum</a> okkar.</p>\n<p><strong>Hlendi</strong> <br/>\n svo a ferli  kringum hlendisjgarinn hafi veri klur  arf a vernda hlendi. Pratar vilja hins vegar gera a  Pratalegan htt: Me lri og frjlsa fr flks a leiarljsi, svo lengi sem vikvm vistkerfi eru varin.</p>\n<p><strong>Uppbygging og uppgrsla</strong> <br/>\nPrata telja brnt a veita rlega fjrmagni til verndar, uppbyggingar og rbta  vinslum fangastum  nttrunni.  vilja Pratar efla skgrkt og landgrslu  landbnaar- og rfoka landi, en   samrmi vi skipulagstlanir og grunnprinsipp, eins og vararregluna.</p>\n<p><strong>Dravernd</strong> <br/>\nPratar eru dravinir <a href=\"https://x.piratar.is/polity/1/issue/404/\">me srstaka draverndarstefnu</a> og vilja endurskoa lg um villt dr og fugla  slandi til a tryggja vernd eirra. Vi viljum annig a spendr  sj njti smu verndar og spendr  landi.  arf a banna veii  villtum drum sem eru  <a href=\"https://www.ni.is/midlun/utgafa/valistar\">vlista Nttrufristofnunar</a>, eins og heimskautaref og sel. Pratar vilja lka gera rkari umhverfiskrfur til fiskeldisfyrirtkja og strefla rannsknir  umhverfishrifum sjvartvegar.</p>\n<p><a href=\"https://piratar.is/frettir/loftslagsstefna-pirata/\">Loftsslagsstefnu Prata m nlgast hr</a></p>\n"},{"category":"atvinnumal","name":"Zaposlitve","statement":"<p>Stefna Prata  atvinnumlum miar a einu: Sjlfbru samflagi sem getur tekist  vi framtina, sjlfvirknivingu og fjru inbyltinguna. Til ess arf margt a spila saman; eins og nskpunarstefna, loftslagsherslur, menntakerfi og verkalsml.</p>\n<p><strong>Nskpun:</strong><br />\n<a href=\"https://x.piratar.is/polity/1/issue/470/\">Nskpunarstefnu Prata er  20 lium</a>.  stefnunni eru margar agerir fyrir nskpunarumhverfi  slandi, auki aljlegt nskpunarsamstarf og nskpun hj hinu opinbera, auk hvata fyrir nskpun  tkni og skapandi greinum um allt land.</p>\n<p><strong>Loftslagsherslur:</strong><br />\nVermta- og atvinnuskpun framtarinnar verur grn. v vilja Pratar styja loftslagsvn fyrirtki og stula a grnni atvinnuuppbyggingu um allt land, eins og sst  <a href=\"https://piratar.is/frettir/loftslagsstefna-pirata/\">loftslagsstefnunni okkar</a>.</p>\n<p><strong>Menntakerfi:</strong><br />\nherslur  menntamlum urfa a breytast samhlia aukinni sjlfvirknivingu.  <a href=\"https://piratar.is/kosningastefna/\">menntastefnu</a> og <a href=\"https://x.piratar.is/polity/1/issue/470/\">nskpunarstefnu</a> Prata er t.d. tala um samfellda frslu um nskpun  menntakerfnu, aukna herslu  frni sem ntist  sjlfvirkum heimi sem og meiri s- og endurmenntun.</p>\n<p><a href=\"https://piratar.is/kosningastefna/\"><strong>Verkalsml:</strong></a> <br />\nMe aukinni sjlfvirknivingu og grnni umbyltingu hagkerfisins vera verkalsml enn mikilvgari. Vi viljum v efla samr vi verkalsflg um atvinnuml og f fulltra starfsmanna  stjrnir fyrirtkja, eins og ekkist  Norurlndum.</p>\n<p><strong>Sjlfbr inaarstefna:</strong><br />\nslandi srvantar framtarsn  atvinnumlum. Okkar fyrsta verk vri v a klra tillgu Prata, <a href=\"https://piratar.is/frettir/samthykkt-island-setur-ser-piratalega-stefnu-til-framtidar/\">sem Alingi samykkti  vor</a>, um a slendingar setji sr sjlfbra inaarstefnu til framtar.</p>\n"},{"category":"heilbrigdismal","name":"Zdravstvo","statement":"<p>Pratar vita a  heilbrigismlum duga engar bkhaldsbrellur. Niurskurur  forvrnum mun einungis leia til aukins kostnaar  heilbrigiskerfinu sar. Sparnaur  vihaldi bygginga leiir til veikinda ba og starfsflks. Mengun mun fram draga <a href=\"https://www.frettabladid.is/frettir/langt-i-ad-island-nai-markmidi-um-faekkun-daudsfalla-vegna-mengunar/\">tugi slendinga til daua  hverju ri</a> ef ekkert er gert. Vi ntum peninginn betur me v a nlgast heilbrigismlin heildsttt.</p>\n<p><a href=\"https://piratar.is/kosningastefna/\">Kosningastefna Prata  heilbrigismlum</a> er rskipt. Kjarni hennar er a heilbrigisjnusta skuli vera gjaldfrjls og fullfjrmgnu, rttindi sjklinga og starfsflks aukin og hersla  skaaminnkun og forvarnir. Hr er stefnan  mjg grfum drttum:</p>\n<p><strong>Heilbrigisml</strong> <br/>\nVi viljum standa vr um opinbera heilbrigisjnustu og efla hana  landsbygginni. Vi viljum a heilbrigisjnusta s gjaldfrjls og tryggja niurgreislu  tannlkningum, augnlkningum, srfrilkningum ofl. Vi viljum gera fjarheilbrigisjnustu a raunverulegum valkosti. Vi stndum me heilbrigisstttum og viljum sj til ess a kjaravirur eirra endi ekki alltaf fyrir gerardmi.</p>\n<p><strong>Geheilbrigi</strong> <br/>\nPratar vilja meferarrri vi gernum vanda sem byggjast  valdeflingu, samykki og samvinnu  sta vingana og frelsissviptinga. Vi viljum niurgreia slfrijnustu, auka fjrmagn  geheilbrigiskerfi og tryggja agengi nemenda a slfrijnustu.</p>\n<p><strong>Skaaminnkun</strong> <br/>\nVi ltum  vmuefnanotendur sem manneskjur, ekki glpamenn. ess vegna arf a afglpava neysluskammta og leggja herslu  gagnreyndar forvarnir, frslu, vihalds- og meferarrri.</p>\n<p><a href=\"https://piratar.is/kosningastefna/\">Nnar hr</a></p>\n"}]

