module.exports = [{"category":"skattamal","name":"Davki","statement":"<p>a er vitlaust gefi.  slandi eru skattar  venjulegt flk me eim hstu sem gerist innan OECD. Hr greia allra tekjuhstu og eignamestu hparnir hins vegar minna til samflagsins en annars staar  Norurlndum og eir tekjulgstu meira. Vi viljum nta skattkerfi til a afla tekna og jafna leikinn  samflagi ar sem eignir flks ra sfellt meiru um tkifri ess og barna eirra. Vi viljum auk ess innheimta fullt gjald fyrir ntingu nttruaulinda  jareign.  kallar rttltara og skilvirkara skattkerfi  a vi tkum  skattaundanskotum af fullri hrku.</p>\n<p>Vi viljum hkka barnabtur annig a fleiri fjlskyldur fi hrri mnaarlegar greislur og hkka grunnlfeyri eldra flks og ryrkja. a viljum vi fjrmagna me v a setja  hflegan streignaskatt  hreina eign umfram 200 milljnir - .e. rkasta 1% samflagsins.  einsetur Samfylkingin sr a rast af krafti gegn alltof hum jaarskttum og vinnuletjandi skeringum.</p>\n<p>Samfylkingin vill taka skattaumhverfi smrri fyrirtkja til endurskounar me a fyrir augum\na stula a auknu jafnri  mrkuum. a er sanngirnisml en leiir lka til fjlbreyttara og\nrttmeira atvinnulfs og lflegri samkeppni en ella til hagsbta fyrir almenning. Vi viljum ltta  regluverki um starfsemi einyrkja til a auvelda eim rurinn en efla um lei agerir gegn gerviverktku.</p>\n"},{"category":"jafnrettismal","name":"Enakopravnost spolov","statement":"<p>Samfylkingin er femnskur flokkur sem vill jafna stu kynjanna og rast af alefli gegn kynbundnum launamun. Vi viljum vinna gegn kerfisbundinni mismunun og fordmum og tryggja jafna mefer  llum svium samflagsins h kynhneig, kynvitund, kyneinkennum ea kyntjningu. sland  a skipa sr  fremstu r  aljavsu hva varar rttarstu hinseginflks, kynsegin flks og flks me dmiger kyneinkenni. Vinna arf fram a bttri rttarstu essara hpa  samvinnu vi flg og fulltra eirra.</p>\n"},{"category":"stjornarskrarmal","name":"Epidemilogija","statement":"<p>Samfylkingin vill a Alingi samykki breytingar  stjrnarskr sem byggjast  tillgum stjrnlagars og eim jarvilja sem kom fram  jaratkvagreislunni ann 20. oktber 2012. Alingi skuldar jinni a ljka v opna og lrislega ferli stjrnlagabreytinga sem hfst eftir hrun. Treysta arf grundvallarinnvii lrissamflagsins og setja valdhfum skrari meginskyldur, byrg og hlutverk en gert er  gildandi stjrnarskr. kvi um jareign aulinda, jaratkvagreislur, framsal valds, jafnt atkvavgi og umhverfisvernd eru  meal eirra fjlmrgu atria sem brnt er a bundin veri  stjrnarskr sem fyrst. </p>\n"},{"category":"evropumal","name":"Evropska integracija","statement":"<p>Lykillinn a farsld slendinga felst  nnum samskiptum vi arar jir, ekki sst innan Evrpu. EES-samningurinn er grundvallaratrii  v samhengi. Hann tekur  ekki til mikilvgra svia, svo sem landbnaar og byggamla, gefur ekki fri  upptku evru og setur EFTA-rkin  stu iggjenda  Evrpusamstarfinu  sta ess a vera jafngildur og burugur aili a sameiginlegum kvrunum.  v skyni a taka mgulegt skref fram  vi  essu samstarfi Evrpuja, er brnt a kanna hug jarinnar um a taka upp aildarvirur vi Evrpusambandi  njan leik. v leggjum vi herslu  a  kosningastefnu Samfylkingarinnar a <ins>haldin veri jaratkvagreisla um framhaldandi virur</ins>.</p>\n<p>mis rlausnarefni ntmans vera ekki leyst nema  aljavettvangi. Loftslagsvin er augljst dmi. Mikilvgt er a sland taki frumkvi  eim mlaflokki  aljavsu, ar e vi getum, skum ekkingar okkar  sjlfbrri orkuframleislu og lfrki hafsins, veri leiandi  eim vettvangi aljlega og veri fyrirmynd fyrir ara sem vilja feta ann veg.</p>\n"},{"category":"sjavarutvegsmal","name":"Gospodarstvo","statement":"<p>Fiskistofnar  slandsmium eru sameign slensku jarinnar. Agangsstring kvtakerfisins \nsjvartvegi er nausynleg til a hmarka viri fiskistofnanna en takmarkanir  veium mynda\num lei aulindarentu sem rennur n nr skipt til eirra sem fara me ntingarleyfi.\nSamfylkingin einsetur sr a tryggja a hagsmunir jarinnar og almennings veri ofan  og\na fullt gjald veri innheimt fyrir fiskveiiaulindina. <ins>Einfaldasta leiin a v marki felst  rlegri\ninnkllun hflegs hluta aflaheimilda og tboi ntingarleyfa til takmarkas tma</ins>. annig skilum\nvi aulindarentunni til rttmts eiganda, almennings.</p>\n"},{"category":"samgongumal","name":"Infrastruktura","statement":"<p>Greiar samgngur um land allt eru lykilatrii vi a tryggja llum jfn tkifri h bsetu. Samfylkingin vill rast  strtak  samgngumlum til a styrkja sland sem heild me v a tengja landshlutana betur saman og stytta vegalengdir innan afmarkara vinnusknarsva. </p>\n<p>Samfylkingin leggur herslu  a fjlga valkostum og auka fjlbreytni  samgngum. Hefja undirbning a Keflavkurlnu, grnni tengingu milli Reykjavkur og Keflavkurflugvallar, og flta Borgarlnu og rum loftslagsvnum framkvmdum  samgngutlun. Byggja upp Landlnu, heildsttt almenningsvagnanet svo a a veri raunhfur kostur fyrir flk a ferast um landi n einkabls. Vi tlum auk ess a gera hjlreiatlun fyrir allt sland og gera tak  lagningu hjlastga um allt land. Allt fellur etta saman vi markmi okkar um lheilsu og  loftslagsmlum. egar kemur a jvegakerfinu arf a vinna  uppsafnari vihaldsrf undanfarinna ra og auka nframkvmdir me hlisjn af atvinnustefnu fyrir sland og herslu  styttingu vegalengda og ryggi.  viljum vi rast  markvissar agerir til a hraa orkuskiptum  samgngum  sj, landi og  flugi, vinna a fjlgun aljlegra fluggtta til landsins, tryggja a gi varaflugvalla su viunandi og a vihald og uppbygging  innvium innanlandsflugs taki mi af uppbyggingu ferajnustu.</p>\n"},{"category":"utlendingamal","name":"Migracije","statement":"<p>Btt staa innflytjenda  slandi er ekki aeins rttltisml heldur lykillinn a farslli uppbyggingu fjlmenningarsamflags ar sem fjlbreytni, gagnkvmur skilningur og jfnuur fara hnd  hnd. Samfylkingin vill lika fyrir atvinnutttku innflytjenda me sveigjanlegra regluverki og stula a v a menntun eirra fist  auknum mli viurkennd og metin til launa. Auvelda arf flki utan EES-svisins a setjast a og vinna  slandi.   slenskukennsla a vera dr ea gjaldfrjls og agengileg  fjlbreyttu formi. Samfylkingin telur a hi opinbera eigi a koma a greislu fyrir slenskunmskei sem su agengileg bi vinnandi flki og einstaklingum n atvinnu. Stjrnvld eiga a leita allra leia til a virkja flk af erlendum uppruna til tttku  samflaginu. Rast arf  markvissar agerir til a vinna gegn fordmum og tryggja a afluttir upplifi sig velkomna. Harka gagnvart umskjendum um aljlega vernd strir gegn grunngildum okkar um samhyg og samstu. Samfylkingin vill rast  gagngera endurskoun  tlendingalggjfinni og mttkukerfi umskjenda um aljlega vernd me mann a leiarljsi og hlisjn af mannrttindasttmlum sem sland hefur stafest. </p>\n"},{"category":"sottvarnarmal","name":"Obramba","statement":"<p> barttunni vi COVID-19 hefur Samfylkingin haft a a leiarljsi a fylgja fyrirmlum sttvarnarlknis og hlusta  raddir srfringa. N er str hluti jarinnar blusettur sem minnkar lkur  dausfllum og smitum en smit berast fram milli einstaklinga. S litli hpur sem er blusettur er samt vikvmum hpum enn tsettur fyrir veikindum en ljst er a grpa arf til agera sem eru til ess fallnar a hgt s a lifa me eim til lengri tma.</p>\n<p>Ungt flk og srstaklega framhaldssklanemar hafa fundi hva mest fyrir afleiingum harra agera og vi viljum v a gripi s til rra sem eru til ess fallin a tryggja a nm og flagslf ungs flks og framhaldssklanema geti fari fram me sem elilegustum htti.</p>\n"},{"category":"loftslagsmal","name":"Podnebne spremembe","statement":"<p>Samfylkingin er strhuga  loftslagsmlum og hfum veri leiandi  mlaflokknum allt kjrtmabili, bi  Alingi og  sveitarstjrnum. Vi tlum a hefja kraftmikla skn  loftslagsmlum, skn sem jafnast  vi strstu samflagsverkefni 20. aldar: raflsingu og hitaveitu, lagningu sma og jvega. En til ess a hrinda essu  framkvmd urfum vi nja nlgun og alvru agerir strax  llum svium samflagsins. N arf plitska forystu um rttlt og sjlfbr umskipti  slensku atvinnulfi, hraari orkuskipti og metnaarfulla grna uppbyggingu um allt land. Um lei og tekist er  vi strstu skorun samtmans vera til tal tkifri til nrrar vermtaskpunar me hagntingu tkni og nskpun  llum svium. Nlgun Samfylkingarinnar  loftslagsmlin hefur talsvera srstu, hn er ntmaleg, framskin og aljleg - og byggir  eirri run sem  bara eftir a halda fram - a mikill meirihluti flks mun ba  bjum og borgum. </p>\n<p>Vi hfum lagt fram 50 agera loftslagspakka <a href=\"xs.is/loftslag\">xs.is/loftslag</a> ar sem hersla er lg  a lgfesta loftslagsmarkmi um a.m.k. 60 prsenta samdrtt  losun, hraa orkuskiptum og strefla almenningssamgngur. Vi eigum a sna metna og nta srstu slands og forskot  orkumlum til a skapa n tkifri. </p>\n"},{"category":"byggdarmal","name":"Pokrajine","statement":"<p>Me markvissri byggastefnu getum vi btt agengi allra landsmanna a almannajnustu, auki tkifri til vermtaskpunar og skapa framski, fjlbreytt og skemmtilegt samflag. Samfylkingin litur a Island sem eina heild ar sem sterkt hofuborgarsvi nytur gos af blomstrandi byggum og bjum um land allt og ofugt. Oabyrg stjornmalaofl ala a sundrungu milli borgar og bygga. a gerir Samfylkingin ekki. Okkar markmi er a tryggja jofn tkifri og moguleika allra landsmanna oha busetu, svo sem til atvinnu, menntunar, menningar og hvers kyns jonustu.</p>\n<p><strong>Meal herslumla Samfylkingarinnar fyrir Alingiskosningarnar 2021:</strong></p>\n<ul>\n<li><ins>Fjrfesta af krafti  grunninnvium</ins>  landsbyggunum, sr  lagi eim sem stula a grnni umbreytingu; fjarskiptum, orkuinnvium, samgngum, hsni o.fl.</li>\n<li>Efla heilbrigisjnustu vi flk um allt land me kvum um starfsastur og kjr heilbrigissttta og fjrfestingu  tknilausnum til fjarlkninga. </li>\n<li><ins>Greia allan ferakostna innanlands vegna nausynlegrar heilbrigisjnustu</ins> vi flk fjarri heimabygg.</li>\n<li>Raast i <ins>storatak i samgongumalum</ins> til a styrkja Island sem heild me vi a tengja landshlutana betur saman og stytta vegalengdir innan afmarkara vinnusoknarsva.</li>\n<li><ins>Byggja upp Landlnu</ins>, heildsttt almenningsvagnanet svo a a veri einfaldur og raunhfur kostur fyrir flk a ferast um sland n einkabls</li>\n<li>Byggja <ins>nskpunarklasa  llum helstu ttbliskjrnum</ins>  samvinnu vi heimaflk.</li>\n</ul>\n"},{"category":"velferdarmal","name":"Socialno varstvo","statement":"<p>Jfnuur og g velferarjnusta fyrir alla eru grunnurinn a rttltu samflagi sem skapar ryggi og efnahagslegan stugleika. Me v a tryggja llum agang a heilbrigis- jnustu, menntun og flagsjnustu, h efnahag og bsetu, dregur a r hrifum stttaskiptingar og veitir fleirum tkifri til fullrar tttku  samflaginu. </p>\n<p>Vi tlum a greia barnafjlskyldum hrri barnabtur og gera a  hverjum mnui til ess a r ntist betur. </p>\n<p>Vi tlum a strbta kjr eldra flks og ryrkja me v a hkka greislur  skrefum. Samfylkingin vill heildarendurskoun almannatrygginga  nsta kjrtmabili. Markmi okkar er a lfeyrir veri ekki lgri en lgstu laun, a dregi veri r skeringum, a frtekjumark lfeyrisgreislna veri fjrfalda upp  100.000 og a frtekjumark atvinnutekna veri refalda upp  300.000.</p>\n<p>Samfylkingin vill lgfesta samning Sameinuu janna um rttindi fatlas flks og koma  ft sjlfstri mannrttindastofnun. Bta arf tkifri fatlas flks til sjlfsts lfs, og brnt er a samningum um notendastra persnulega asto veri fjlga jafnt og tt.  </p>\n<p>Samfylkingin vill tryggja olendum  kynferisbrota- og heimilisofbeldismlum mlsaild egar rtta er yfir geranda, stytta mlsmeferartma, lgbinda rtt til launas leyfis  kjlfar brots, rmka gjafsknarreglur og tryggja brotaolum langtmastuning og ryggi mean ml eru til rannsknar.\nHr m lesa meira <a href=\"https://xs.is/fjolskyldur-i-forgang\">https://xs.is/fjolskyldur-i-forgang</a></p>\n"},{"category":"husnaedismal","name":"Stanovanjska problematika","statement":"<p>Vi tlum a hafa forystu um grundvallarstefnubreytingu  hsnismlum til a skapa heilbrigan hsnismarka til framtar. Vi teljum a lausnin felist  strauknum framlgum til a byggja hagkvmt hsni. a dregur r sveiflum og neikvum hrifum  verlag og vexti. Fjlskyldur eiga rtt  hsnisryggi hvort sem r eiga heimili sitt ea leigja. Hsniskostnaur er mesti tgjaldaliur flestra heimila og versveiflur  fasteignamarkai, miklar breytingar  vxtum og fyrirsjanleiki um leigu skapa ryggi og ta undir jfnu og ftkt.</p>\n<p>Helstu herslur</p>\n<ul>\n<li><p>Byggja 1000 leigu- og bseturttarbir  hverju ri me hsnisflgum n hagnaarsjnarmia, sem yru  1/3 af rlegu byggingarmagni. a kallar  tvfldun stofnframlaga. Me v fjlgum vi bum um allt land fyrir tekjulgri hpa og temprum ver fyrir alla. annig lkkar hsaleigan.</p>\n</li>\n<li><p>Styja vi rannsknir og nskpun  run bygginga og hsnis til a n niur byggingarkostnai og byggja n grn barhverfi.</p>\n</li>\n<li><p>Huga srstaklega a v a tryggja frambo  bum fyrir ungt flk og hsniskjarna fyrir eldra flk.</p>\n</li>\n<li><p>Fra hsnis- og byggingarml undir eitt runeyti sem hefur yfirsn og ber byrg  uppbyggingu um land allt.</p>\n</li>\n</ul>\n"},{"category":"menntamal","name":"olstvo","statement":"<p>Vi viljum raunverulegt jafnrtti til nms  llum sklastigum. Me fjlbreyttu menntakerfi sem byggist  jfnui rktum vi styrkleika hvers og eins. annig bum vi best  haginn fyrir samflagi allt til framtar. Vi viljum skapa menntakerfi ar sem enginn er skilinn eftir, og hver nemandi fr frslu og tekur t roska  eigin forsendum. N arf srstaklega a styja vi framhaldssklanema og hsklanema sem hafa urft a sta takmrkunum til nms og flagslfs, og grpa til agera til a fora brottfalli.</p>\n<p><strong><ins>Helstu herslur:</ins></strong> </p>\n<ul>\n<li><ins>Hkka grunnframfrslu</ins> stdenta og hkka frtekjumark nmslna.</li>\n<li><ins>Styrkja nskpun og runarstarf  menntamlum, efla stojnustu</ins> vi kennara og nemendur og bta agengi a slfringum, flagsrgjfum og annarri stuningsjnustu.</li>\n<li>Styja sveitarflg og skla ar sem htt hlutfall barna hefur slensku ekki a murmli svo <ins>efla megi slensku- og murmlskennslu</ins>  llum sklastigum.</li>\n<li>Auka vi framlg til rannsknar- og vsindasja og lika fyrir tttku slendinga  <ins>aljlegum nskpunar- og rannsknarverkefnum</ins>.</li>\n<li>Vinna <ins>markvisst gegn undirfjrmgnun hsklastigsins</ins> annig a fjrmgnun veri sambrileg og  Norurlndunum.</li>\n<li>Efla listnm me <ins>lkkun sklagjalda vi Listahskla slands</ins> og greiara agengi flks a listnmi um allt land. </li>\n<li><ins>Efla in-, verk- og starfsnm</ins> og auka ar frambo til ess a fleiri fi inngngu  sklana.\nHr m lesa meira <a href=\"https://xs.is/sterkara-samfelag\">https://xs.is/sterkara-samfelag</a></li>\n</ul>\n"},{"category":"umhverfismal","name":"Varstvo okolja","statement":"<p>Samfylkingin hefur kynnt 50 alvru agerir sem Samfylkingin skar eftir umboi kjsenda til a hrinda  framkvmd. Vi tlum a hefja kraftmikla skn  loftslagsmlum sem jafnast  vi strstu samflagsverkefni 20. aldar: Raflsingu og hitaveitu, lagningu sma og jvega. Til ess a hrinda essu  framkvmd urfum vi nja nlgun og alvru agerir strax  llum svium samflagsins. \na arf plitska forystu um rttlt og sjlfbr umskipti  slensku atvinnulfi, hraari orkuskipti og metnaarfulla grna uppbyggingu um allt land. Samfylkingin trir v a engin af stru skorunum mannkyns vera sigraar nema  mjg fjljlegu samstarfi. \nHr m lesa nnar um agerirnar 50. <a href=\"https://xs.is/loftslag\">https://xs.is/loftslag</a></p>\n<p><strong><ins>Helstu herslur</ins></strong></p>\n<ul>\n<li>Lgfesta loftslagsmarkmi um a.m.k. 60 prsenta samdrtt  losun grurhsalofttegunda fyrir ri 2030. </li>\n<li>Mta nja og miklu metnaarfyllri ageratlun </li>\n<li>Hefja undirbning a Keflavkurlnu og flta Borgarlnu  </li>\n<li>Gera hjlreiatlun fyrir allt sland. </li>\n<li>Hraa orkuskiptum  samgngum. </li>\n<li>Stofna grnan fjrfestingasj. </li>\n<li>Rast  umbtur  landbnaarkerfinu n ess a draga r stuningi vi bndur </li>\n<li>Banna alfari flutninga og notkun svartolu  slenskri landhelgi, rafva hafnir og leggja bann vi oluborun  efnahagslgsgunni. </li>\n<li>Styja markvisst vi tknilausnir til kolefnisfngunar- og frgunar. </li>\n<li>Taka upp grna utanrkisstefnu </li>\n</ul>\n"},{"category":"atvinnumal","name":"Zaposlitve","statement":"<p>Samfylkingin boar nja og spennandi atvinnustefnu. Vi vitum a a flug velferarjnusta - fjlbreytt menntatkifri og flugt heilbrigiskerfi - er grundvllur ess a atvinnulfi geti blmstra. Okkar atvinnustefna byggir v  a styrkja innvii, skja fram fyrir aljageirann, vinna a stafrnni umbreytingu atvinnulfsins. Styja vi tkni, nskpun og skapandi greinar og auka annig fjlbreytni.</p>\n<p>Strsta atvinnuml essa ratugar er svo rttlt og sjlfbr umbreyting  efnahags- og atvinnulfi til a mta grnni framt. Vi orum a setja fjrmagn  fjrfestingar sem vi vitum a munu skila sr  aukinni atvinnuskpun sar,  tengslum vi htkniina, spennandi rlausnarml  tengslum vi breytta heimsmynd og mlefni nttrunnar og loftslagsins.</p>\n<p>Vi stndum me litlum fyrirtkjum; ess vegna tlum vi a setja frtekjumark fyrir tekjuskatt eirra sem eykur sveigjanleika  rekstri og skapar hvata til fjrfestinga. Og vi viljum efla samkeppniseftirlit til muna.</p>\n<p>Vi urfum a efla tkifri flks til nskpunar um allt land. ess vegna tlum vi a byggja nskpunarklasa  llum helstu ttbliskjrnum  samvinnu vi heimaflk. ar sem frumkvlar, fyrirtki og stofnanir f astu og stuning. Vi viljum einnig rast  sjlfbra uppbyggingu ferajnustu um allt land, mta feramlastefnu og standa fyrir samstilltu taki til vihalds og uppbyggingar um allt land.</p>\n"},{"category":"heilbrigdismal","name":"Zdravstvo","statement":"<p>a rkir jarstt um nausyn ess a byggja upp betri heilbrigisjnustu fyrir okkur ll, h efnahag og bsetu, og n verur ekki lengur bei eftir brnum agerum. a arf bi a leysa brnustu vandamlin og byggja upp jnustuna til lengri tma.  bak vi hvert nafn  bilista eftir nausynlegum agerum, heimsknum til srfringa og vitlum vi slfringa og gelkna eru fjlskyldur sem urfa lausn  snum vanda. Samfylkingin telur tma til kominn a hlusta  raddir heilbrigisstarfsflks sem hefur lengi kalla eftir umbtum.  samvinnu vi a urfum vi a bta heilbrigisjnustuna me llum rum. </p>\n<p>Samfylkingin tlar sna vi blainu  heilbrigismlum. Meal helstu herslumla okkar er a:</p>\n<ul>\n<li><ins>Auka fjrmagn</ins> til opinberrar heilbrigisjnustu, og rast  agerir gegn undirmnnun og stytta bilista eftir brnum agerum</li>\n<li>Rast  <ins>jartak  geheilbrigisjnustu</ins> um allt land. Gera geheilbrigisjnustu a hluta af almenna heilbrigiskerfinu. Byggja ntmalegar gedeildir og vinna markvisst niur bilista barna og ungmenna eftir nausynlegri jnustu. <ins>Innleia gjaldfrjlsa slfrijnustu fyrir brn og ungmenni.</ins></li>\n<li>Ltta lagi af Landsptala me kerfisbreytingum, njum bsetu- og jnusturrum fyrir eldra flk, aukinni heimajnustu og markvissri fjlgun hjkrunarrma. </li>\n<li>Styrkja forvarnir. Bta strax r fyrirkomulagi <ins>skimana fyrir leghls- og brjstakrabbameini</ins> og taka upp skimanir fyrir fleiri gerum krabbameins.</li>\n</ul>\n"}]

