module.exports = [{"category":"skattamal","name":"Davki","statement":"<p>Vi lofum lkkun skatta   gu heimila og fyrirtkja. Skattakerfisbreytingar sem Sjlfstisflokkurinn hefur haft forystu um allt fr 2013 hafa mia a v a ltta byrar launaflks og auka kaupmtt, styrkja afkomu fyrirtkja, hvetja au til fjrfestinga og byggja undir nskpun og run.</p>\n"},{"category":"jafnrettismal","name":"Enakopravnost spolov","statement":"<p>Sjlfstisstefnan grundvallast  frelsi og byrg, jafnrtti og samkennd. Hn er vsn og frjlslynd framfarastefna.</p>\n<p>Sjlfstismenn hafa einstaklingsfrelsi, atvinnufrelsi og sreignarrtt  hvegum, me hagsmuni allra fyrir augum. Sjlfstisflokkurinn vill tryggja jfn tkifri allra og fjrhagslegt sjlfsti einstaklinga sem er grundvllur jafnrttis. Vi teljum a grundvallaratrii a tryggja jafna stu og jfn tkifri einstaklinga h kyni, stu, uppruna ea rum ttum.</p>\n"},{"category":"stjornarskrarmal","name":"Epidemilogija","statement":"<p>Stjrnarskrin er grundvllur stjrnskipunar landsins. Stjrnarskrin kveur  um grundvallarreglur lveldisins og mannrttindi borgaranna og hefur teki nausynlegum breytingum  lveldistmanum. Tilefni er til endurskounar kveinna tta stjrnarskrrinnar en hyggja arf vel a eim breytingum og rrm arf a gefast, bi innan ings og utan, til a gaumgfa tillgur a breytingum  henni. Heildarendurskoun sem umturnar llum kvum stjrnarskrrinnar samrmist illa sjnarmium um rttarryggi og fyrirsjanleika. Mikilvgt er a fylgja kvum stjrnarskrr um hvernig standa skuli a breytingum  grunnlgum landsins. A sama skapi telur Sjlfstisflokkurinn mikilvgt a breytingar  stjrnarskr su gerar  vtkri plitskri stt.</p>\n"},{"category":"evropumal","name":"Evropska integracija","statement":"<p>Evrpa er mikilvgasta markas- og menningarsvi slands. Mikilvgt er a tryggja fram opinn og frjlsan agang a innri markai Evrpusambandsins  grundvelli samningsins um Evrpska efnahagssvi. randi er a haldi veri fram a efla hagsmunagslu innan ramma EES og tryggja a mguleikar slands til hrifa  fyrri stigum EES mla veri nttir til fulls. Sjlfstisflokkurinn gerir verulegar athugasemdir vi a tekin s upp lggjf  EES-samninginn sem felur  sr valdheimildir sem fellur utan ramma tveggja stoa kerfis samningsins. sland  miki undir v a rkta g viskiptasambnd vi sem flest rki heimsins. Sjlfstisflokkurinn telur mikilvgt a frverslunarsamningum vi rki utan Evrpska efnahagsvisins (EES) veri fjlga, tvhlia ea  samvinnu vi nnur aildarrki EFTA. rtta er a hagsmunir slands eru best tryggir me v a standa utan Evrpusambandsins. Mikilvgt er a tryggja a aildarvirur veri ekki teknar upp a nju n samykkis  jaratkvagreislu.</p>\n"},{"category":"sjavarutvegsmal","name":"Gospodarstvo","statement":"<p>slenskur sjvartvegur er burars  atvinnulfi um land allt og leiandi  heiminum hva varar sjlfbrni, run og arsemi. Atvinnugreinin er  daglegri samkeppni vi erlendan rkisstyrktan sjvartveg. Nausynlegt er a gjaldheimta  sjvartvegi dragi ekki r samkeppnishfni  aljamarkai og fjrfestingu  greininni. Fiskeldi er vaxandi hluti slensks sjvartvegs og gefur aukin tkifri til vermtaskpunar. Vi frekari uppbyggingu ber a leggja herslu  ga umgengni samt mtvgisagerum, svo lag  vistkerfi s lgmarka og sjlfbrni trygg.</p>\n"},{"category":"samgongumal","name":"Infrastruktura","statement":"<p>Sjlfstisflokkurinn leggur rka herslu  a bta samgngur me hraari uppbyggingu og skynsamlegri ntingu fjrmuna, meal annars me samstarfsverkefnum rkis og einkaaila. annig stulum vi a meira ryggi, bttu loftslagi, sterkari byggum, atvinnuskpun og auknum hagvexti. Aukum frelsi flks egar kemur a v a velja sr samgngumta, hvort sem um er a ra almenningssamgongur, almenna bifreiaumfer, gangandi ea hjolandi. Einn samgongumati a ekki a rengja a orum.</p>\n<p>Mikilvgt er a tryggja arbra nytingu fjarmuna annig a markmi um greiari samgongur, aukin lifsgi og valfrelsi i samgongum naist. Aukin hersla veri lg  uppbyggingu stofn- og tengivega um land allt. Mta skal tlun um ger jarganga til lengri tma, styrkingu ferjuleia og vihald flugvalla og uppbyggingu varaflugvalla. Greiar og ruggar samgngur eru mikilvgar undirstur atvinnulfs og styrkja samkeppnishfni byggarlaga landsins. Reykjavkurflugvllur gegnir mikilvgu ryggishlutverki fyrir allt landi og er v brnt a hann veri breyttur  Vatnsmri ar til annar jafn gur ea betri kostur er tilbinn til notkunar.</p>\n"},{"category":"utlendingamal","name":"Migracije","statement":"<p>Innflytjendur auga bi menningu og efnahag. Auvelda tti flki utan Evrpska efnahagssvisins, sem getur fengi starf hr  landi, a koma hinga og starfa. Stytta arf og einfalda alla ferla og kerfi er vara umskjendur um aljlega vernd. tlendingalggjfina arf a ra fram af byrg, raunsi og mann.</p>\n"},{"category":"sottvarnarmal","name":"Obramba","statement":"<p>Sjlfstisflokkurinn leggur herslu  a  frjlsu samflagi er mikilvgt a stjrnvld viri grunnrttindi borgaranna.  upphafi faraldursins var gripi til umfangsmikilla sttvarnaagera til a verja lf og heilsu landsmanna og tryggja um lei a heilbrigiskerfi gti sinnt nausynlegri jnustu. slendingar hafa veri  forystu ja  barttunni vi veiruna. N egar nr ll jin er blusett verur a slaka  sttvarnaagerum annig a daglegt lf frist  elilegt horf. Nausynlegt er a halda fram a vega hagsmuni t fr sttvrnum og efnahags- og samflagslegum hrifum. Leggja  aukna herslu  einstaklingsbundnar varnir. Um lei verur a huga srstaklega a eim sem glma vi undirliggjandi sjkdma ea eru af rum skum  vikvmri stu. Sttvarnaagerir til lengri tma geta ekki teki mi af stu Landsptala heldur arf skipulag og stjrnun sptalans a taka mi af astum  hverjum tma. Auka verur svigrm heilbrigiskerfisins til a bregast vi, jafnt fjrhagslega sem skipulagslega. Standa verur fram vr um fleira en sttvarnir egar kemur a heilsu landsmanna.</p>\n"},{"category":"loftslagsmal","name":"Podnebne spremembe","statement":"<p>a er skylda okkar a vinna gegn loftslagsvnni og verja annig lfsgi okkar og komandi kynsla. ess vegna tlum vi fram a vera  fararbroddi egar kemur a nausynlegum agerum til a stemma stigu vi loftslagsbreytingum.  agerunum felast einnig efnahagsleg tkifri. Helsta framlag slands til umhverfismla eru orkuskiptin. Staa slands  orkumlum er fundsver og gefur metanleg tkifri til a vera leiandi  grnni orkubyltingu, hverfa fr jarefnaeldsneyti og taka upp umhverfisvnni orkugjafa.</p>\n<p>Grna orkubyltingin kallar  aukna notkun  bi raforku og rafeldsneyti sem sland er kjrstu til a framleia.</p>\n<p>Efnahagslegur vinningur af grnni orkubyltingu er mikill en rlega verja slendingar um 80-120 milljrum krna  eldsneytiskaup fr tlndum.</p>\n<p>a er raunhft fyrir sland a skipta um orkugjafa  blum,  sj og  flugi. annig getum vi nota okkar eigin grnu orku, spara gjaldeyri og um lei skapa nja, hugvitsdrifna atvinnugrein.</p>\n<p>Sjlfstisflokkurinn vill a sland stigi skrefi til fulls, s leiandi  orkuskiptum  heimsvsu me metnaarfullum agerum og veri fyrst ja h jarefnaeldsneyti.</p>\n<p>Sjlfstisflokkurinn minnir  a slendingar eiga miki undir umhverfinu og sjlfbrri ntingu nttruaulinda. Sjlfbr run mtir krfum samtmans n ess a skera mguleika komandi kynsla. Hn byggist  remur meginstoum; vistfrilegri, flagslegri og efnahagslegri, sem eru askiljanlegar og har hver annarri.</p>\n"},{"category":"byggdarmal","name":"Pokrajine","statement":"<p>Frelsi til vals um bsetu er mikilvgt. Sjlfstisflokkurinn leggur herslu  a me flugri grunnjnustu veri bygg um allt land best trygg. Tkifri landsbygganna til aukinna lfsga ba vera best trygg me tryggu og ngu rafmagni, fjlbreyttri menntun, gum samgngum og flugum fjarskiptum. Einnig er brnt a forsendur skapist fyrir fjlbreyttu nmsframboi, jafnt me stabundnu nmi og fjarnmi. Mtu veri heildst og framskin byggastefna til lengri tma me herslu  jfn tkifri til atvinnu og bsetu. Grunnur a auknum lfsgum er lagur me v a nta gi landsins sem best. Uppbygging og stuningur vi nskpun treystir bsetuskilyri  landinu.</p>\n"},{"category":"velferdarmal","name":"Socialno varstvo","statement":"<p><strong><ins>Eldri borgarar:</ins></strong> Sjlfstisflokkurinn mun beita sr fyrir v a strax 1. janar 2022 veri frtekjumark atvinnutekna hkka  200 sund krnur  mnui. etta er fyrsta skref. Sjlfstisflokkurinn vill stokka upp almannatryggingakerfi eldri borgara  samvinnu vi hagsmunasamtk eirra og lfeyrissji.   vegum Sjlfstisflokksins hefur starfshpur, undir forystu dr. Vilhjlms Egilssonar, unni a tillgum til tbta  lfeyriskerfi eldri borgara. Tillgurnar mia a v a styrkja stu eirra sem eru me takmrku rttindi  lfseyrissjum og hverfa fr skeringum. etta veri gert me srstakri lfeyrisuppbt sem tekur mi af rttindum hvers og eins  lfeyrissji.</p>\n<ul>\n<li>Me lfeyrisuppbt, sem rki fjrmagnar, er veri a jafna stu eldri borgara gagnvart ellilfeyri r lfeyrissjum.</li>\n<li>annig verur llum eldri borgurum tryggar sttanlegar tekjur.</li>\n</ul>\n<p>Arar skattskyldar tekjur eldri borgara  s.s. atvinnutekjur, sreignasparnaur, fjrmagnstekjur  eiga ekki a skera lfeyrisuppbtina en vera skattlagar me sama htti og eftir smu reglum og skattskyldar tekjur annarra. Lfeyrisuppbt  a taka smu breytingum og lfeyrir r lfeyrissjum.</p>\n<p><strong><ins>ryrkjar:</ins></strong> Sjlfstisflokkurinn leggur herslu  a tryggingakerfi ryrkja veri endurskoa fr grunni. a einfalda, me a a markmii a tryggja fjrhagslegt sjlfsti ryrkja og stula a v a ryrkjar hafi vinning af v a afla sr tekna. Um lei vill Sjlfstisflokkurinn innleia persnubundna endurkomusamninga sem auveldar ryrkjum a taka tt  almennum vinnumarkai me tilliti til starfsgetu n ess a eir veri fyrir umtalsverri skeringu  rorkulfeyri eins og n er.</p>\n<h3 id=\"fj-lskyldum-l\">Fjlskylduml</h3>\n<p>Foreldrar: N lg um fingarorlof voru samykkt undir lok sasta rs. ar skipti mestu a orlofstminn var lengdur  12 mnui. Grunnger fingarorlofsins hlst breytt annig a orlofsrttindi miast vi tttku og tekjur  vinnumarkai fyrir fingarorlof. eir foreldrar sem n lgmarksttku  vinnumarkai og nmsmenn f hins vegar fingarstyrk. Hkka arf fjrh fingarorlofs.</p>\n<p>Sjlfstisflokkurinn vill styja foreldra utan vinnumarkaar og nmsmenn srstaklega me jkvum hvtum til barneigna. ess vegna er nausynlegt a endurskoa reglur um fingarstyrk og hkka hann verulega. </p>\n"},{"category":"husnaedismal","name":"Stanovanjska problematika","statement":"<p>Sjlfstisflokkurinn leggur herslu  ruggt bahsni, hvort sem er til eignar ea leigu. A hafa ak yfir hfui er ein af grunnrfum mannsins og leggja ber herslu  fjlbreytt og hagkvm hsnisrri. Sjlfstisflokkurinn mun fjarlgja hindranir og auvelda ungu flki a eignast eigi hsni, en einnig stula a virkum leigumarkaur eins og ekkist vast hvar  ngrannalndunum.</p>\n<p>A frumkvi Sjlfstisflokksins var lgfest til frambar 10 ra skattfrjls rstfun sreignasparnaar vegna fyrstu bakaupa. Sjlfstisflokkurinn vill a tmabundin heimild annarra til a rstafa sreignasparnai inn  baln veri til frambar, samkvmt smu reglum og gilda um fyrstu kaup.</p>\n<p>Byggingarkostnaur barhsnis  slandi er einn s hsti  Evrpu. S umgjr sem yfirvld hafa mta fyrir byggingarinainn er skilvirk, tmafrek og dr. Allar hindranir sem hamla v a framkvmdaailar geti brugist tmanlega vi markasastum magna sveiflur, ta undir stugleika og auka annig hsniskostna sem getur svo valdi lgu  vinnumarkai. Fasteignaver hr  landi er hrra en a yrfti a vera. Sjlfstisflokkurinn tlar a leia tgnguna t r frumskgi reglugera og hamlandi opinberra afskipta af byggingarinainum.</p>\n"},{"category":"menntamal","name":"olstvo","statement":"<p>Sjlfstisflokkurinn leggur herslu  fjlbreytt og jfn tkifri til menntunar, og a auk opinbers rekstrar su kostir einstaklingsframtaksins nttir me flugum sjlfsttt starfandi sklum og nskpun  sem flestum svium. Hi opinbera  a greia a sama me hverjum nema, h rekstrarformi sklans sem hann skir.</p>\n<p>Sklastarfi arf a mia meira a rfum hvers og eins annig a nemendur ni rangri  snum forsendum. Srstaklega arf a huga a brnum me erlent murml og drengjum, en eir standa hllum fti samkvmt rannsknum og aljlegum samanburi. Grunnsklar eiga a leggja aukna herslu  list- og verkgreinar til a ba nemendum betri grunn fyrir nm  eim svium  framhaldssklastigi.</p>\n<p>In- og tkninm er ein grunnsto fjlbreytts og flugs atvinnulfs og tir undir nskpun. Sjlfstisflokkurinn fagnar auknum huga ungs flks  eim greinum. Tryggja verur a menntastofnanir geti mtt eim huga og sinnt svaxandi rfum atvinnulfsins fyrir in- og tknimennta starfsflk. Gera  strtak  netvingu og tryggja agang a stafrnu hsklanmi  slandi. Flk  a geta mennta sig h bsetu, fjlskylduastum, fjrhag ea vinnu. Styrkja skal hsklanm um allt land og tryggja gi ess.</p>\n"},{"category":"umhverfismal","name":"Varstvo okolja","statement":"<p>sland og slendingar eiga vera  forystu varandi nttruvernd, loftslags- og umhverfisml  heimsvsu me skynsamlegri ntingu orkuaulinda  gu sjlfbrni, fjlbreytileika og grnnar vermtaskpunar.</p>\n<p>Eftir fremsta megni skal horft til ess vi opinber innkaup a umhverfisftspor vru ea jnustu s sem minnst og s ess kostur a innkaup rkisins hafi jkvi hrif  umhverfi.</p>\n<p>Setja skal samrmdar reglur um sorpflokkun fyrir allt landi og stefna a eins mikilli endurvinnslu innanlands og kostur er. annig eflast hringrsarhagkerfi og vermtaskpun  grundvelli hugvits, vsinda, tkni og nskpunar.</p>\n<p>Allur opinber stuningur vi atvinnustarfsemi og leyfisveitingar til bnda, tgera og annarra fyrirtkja skal vera  grundvelli sjlfbrni og framrskarandi umgengi vi aulindir til lands og sjvar. Meginreglan s a stuningi ea vilnunum af hendi hins opinbera fylgi samflagsleg byrg varandi ntttvernd, umhverfis- og loftslagsml.</p>\n<p>Helsta framlag slands til umhverfismla eru orkuskiptin. Staa slands  orkumlum er fundsver og gefur metanleg tkifri til a vera leiandi  grnni orkubyltingu, hverfa fr jarefnaeldsneyti og taka upp umhverfisvnni orkugjafa.</p>\n"},{"category":"atvinnumal","name":"Zaposlitve","statement":"<p>Grundvallarstefna Sjlfstisflokksins er a frumkvi einstaklingsins fi noti sn samfara byrg  eigin athfnum. flugt og fjlbreytt atvinnulf er forsenda framfara og undirstaa  velferarkerfisins. Stjrnvld eiga a skapa heilbrigt og hvetjandi umhverfi fyrir atvinnulfi svo nta megi krafta einstaklinganna til fulls. Ltil og mealstr fyrirtki eru hryggjarstykki slensks atvinnulfs. Huga verur srstaklega a umhverfi eirra er varar skatta, gjld og regluverk til a styrkja samkeppnisstu eirra.</p>\n<p>Undir forystu Sjlfstisflokksins hafa veri stigin markviss skref til a einfalda regluverk atvinnulfsins, auka samkeppnishfni ess og ta undir stofnun nrra fyrirtkja og nskpun. Enn er  miki verk unni. a arf a vera einfalt a stofna og reka fyrirtki og a arf a vera einfalt fyrir fyrirtki a ra starfsflk. Skattaumhverfi verur a sna me eim htti a tryggja aljlega samkeppnishfni og annig a almennt s ekki rf fyrir srtkar vilnanir ea afsltt af opinberum gjldum. Aukin framleini er forsenda hagvaxtar og aukin framleini byggir  nskpun. ess vegna er mikilvgt a stjrnvld skapi umhverfi sem hvetur til nskpunar  llum svium atvinnulfsins.</p>\n"},{"category":"heilbrigdismal","name":"Zdravstvo","statement":"<p><strong>Sjalfstisflokkurinn vil taka forystu  velferar- og heilbrigismlum og mta nja stefnu a breium grunni  opinberum rekstri og einkarekstri.</strong> Stefnan skal m.a. n til menntunar heilbrigisstarfsflks, tknirunar og arfa flks.</p>\n<p>Sjlfstisflokkurinn leggur herslu  a vi skipulag heilbrigisjnustu s rttur flks til jnustu tryggur. Me lgbundinni jnustutryggingu veri flk sett  fyrsta sti. Sjkratryggingar beri byrg  v a s sem arf  jnustu a halda fi hana innan kveins tma  sttanlegs bitma.</p>\n<p>Nskpun  llum svium velferar- og heilbrigisjnustu getur stula a umtalsvert aukinni hagkvmni og gum  rekstri og tkifri til aukinna tflutningstekna Nta m rafrnar/stafrnar lausnir  auknum mli. Efla ber srstaklega forvarnir, endurhfingu og geheilbrigisjnustu. Slkt getur dregi r rorku og auki lfsgi almennings til muna.</p>\n<p>Nta arf einkaframtaki betur  svii heilbrigisjnustu og fjrfestinga. Sjkratryggingar slands vera a rkja betur sitt hlutverk og sjklingar og heilbrigisstarfsflk urfa a hafi fleiri en einn valkost egar kemur a almennri heilbrigisjnustu. Ganga arf fr samningum vi rekstraraila  heilbrigisjnustu. Langir bilistar og tvfallt heilbrigiskerfi eru jarskmm. Allir eiga a njta grar jnustu n tillits til efnahags- ea jflagsstu.</p>\n<p>Samningar urfa a liggja fyrir um kaup  jnustu vi sjkrastofnanir og srfringa  svii velferarjnustu fyrir alla landsmenn og jafnri skal rkja  milli landsbyggar og hfuborgarsvisins.</p>\n"}]

